// Internal
import { CampaignRouteMarker } from "./campaign-route-marker.model";

export class CampaignRoute {
  id: string;
  name: string;
  campaignTypeId: number;
  latitude: number;
  longitude: number;
  zoom: number;

  // TODO: Get and Set using formFields
  campaignRouteMarkers: CampaignRouteMarker[] = [];

  map: google.maps.Map;

  constructor(values: Object = {}) {
    Object.assign(this, values);
  }

  applyFormFields(map: google.maps.Map) {
    if (map) {
      var center = map.getCenter();
      this.latitude = center.lat();
      this.longitude = center.lng();
      this.zoom = map.getZoom();
    }

  }
}
