// External
import { Component} from '@angular/core';

// Internal
import { AnalyticsService } from '../analytics/analytics.service';
import { ExportAttendeesService } from './export-attendees.service';
import { EventProvider } from '../event/event.provider';
import { EventObject } from '../event/event.models/event-object.model';

import { SortPipe } from '../pipes/sort.pipe';
import { PhonePipe } from '../pipes/phonenumber.pipe';

import { faCloudDownloadAlt } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'attendee-list',
  templateUrl: './attendee-list.component.html',
  styleUrls: ['./attendee-list.component.css']
})

export class AttendeeList {
  public selectedEvent: EventObject;

  public hasSales: boolean = false;

  public loading: boolean = false;
  salesByAttendee: string[];
  exportAttendeeList: Object[];
  viewAttendees: boolean = false;

  headers = ['Name', 'Email', 'Gender', 'Phone', 'Ticket Class', 'Birth Date'];

  faCloudDownloadAlt = faCloudDownloadAlt;

  //    May need this to get EventId
  //    constructor(private analyticsService: AnalyticsService, private eventProvider: EventProvider, private eventAttendee: EventProvider) {

  constructor(private analyticsService: AnalyticsService, private exportService: ExportAttendeesService, private eventProvider: EventProvider) {
    this.selectEvent(eventProvider.selectedEventObject);

    var self = this;

    var subscription = eventProvider.selectedEvent.subscribe(
      function (x: EventObject) {
        self.selectEvent(x);
      },
      function (err) {
        console.log('Error: ' + err);
      },
      function () {
        console.log('Completed');
      }
    );
  }

  selectEvent(eventObject) {
    this.selectedEvent = eventObject;
    this.hasSales = false;
    this.loading = true;

    if (this.selectedEvent != null) {
      this.eventProvider.updateSales(this.selectedEvent.id).subscribe((refreshSalesResult) => {
        this.getSalesByAttendee(this.selectedEvent.id);
      }, (errorResult) => {
        console.log(errorResult);
      });
    }
  }

  getSalesByAttendee(eventId: string) {
    // console.log("=== getSalesByAttendee::eventId is " + eventId);
    this.analyticsService.getSalesByAttendee(eventId).subscribe((res) => {
      // console.log("=== getSalesByAttendee::res is " + res);
      if (res.length > 0) {
        this.hasSales = true;

        this.salesByAttendee = res;
        this.exportAttendeeList = res;
      }
    });
  }

  showAttendees() {
    this.viewAttendees = true;
  }

  hideAttendees() {
    this.viewAttendees = false;
  }

  exportCSV() {
    var date = this.selectedEvent.startTime;
    var event = this.selectedEvent.name;

    var fileName = date + "_" + event + "_AttendeeList.csv";

    this.exportService.exportToCsv(fileName, this.exportAttendeeList);
  }
}
