export class CampaignType {
  id: number;
  name: string;
  icon: string;
  defaultRate: number;
  enabled: boolean;
  order: number;

  constructor(id: number, name: string, icon: string, defaultRate: number, enabled: boolean, order: number) {
    this.id = id;
    this.name = name;
    this.icon = icon;
    this.defaultRate = defaultRate;
    this.enabled = enabled;
    this.order = order;
  }
}
