// External
import { Injectable } from '@angular/core';

// Internal
import { CampaignType } from './campaign.models/campaign-type.model';

@Injectable()
export class CampaignTypeProvider {
  private campaignTypes: CampaignType[] = new Array();
  private iconPath: string = "../../NgApp/assets/icons/";

  constructor() {
    // TODO: Define these elseware
    this.campaignTypes.push(new CampaignType(1, "Link",       this.iconPath + "link.png",        1.0,  true, 6));
    this.campaignTypes.push(new CampaignType(2, "Facebook",   this.iconPath + "facebook.png",    1.0,  true, 1));
    this.campaignTypes.push(new CampaignType(3, "Twitter",    this.iconPath + "twitter.png",     1.0,  true, 2));
    this.campaignTypes.push(new CampaignType(4, "Instagram",  this.iconPath + "instagram.png",   0.0,  true, 3));
    this.campaignTypes.push(new CampaignType(5, "PromoterQR", this.iconPath + "promoterqr.png",  1.0,  true, 7));
    this.campaignTypes.push(new CampaignType(7, "Location",   this.iconPath + "handbill.png",    10.0, true, 4));
    this.campaignTypes.push(new CampaignType(8, "Poster",     this.iconPath + "poster.png",      10.0, true, 5));
  }

  getCampaignTypes(): CampaignType[] {
    return this.campaignTypes;
  }

  getCampaignTypeName(campaignTypeId: number): string {
    var campaignType = this.campaignTypes.find(campaignType => campaignType.id == campaignTypeId);

    return !!campaignType ? campaignType.name : null;
  }

  getIcon(campaignTypeId: number): string {
    var campaignType = this.campaignTypes.find(campaignType => campaignType.id == campaignTypeId);

    return !!campaignType ? campaignType.icon : null;
  }

  getName(campaignTypeId: number): string {
    var campaignType = this.campaignTypes.find(campaignType => campaignType.id == campaignTypeId);

    return !!campaignType ? campaignType.name : null;
  }

  getDefaultRate(campaignTypeId: number): number {
    var campaignType = this.campaignTypes.find(campaignType => campaignType.id == campaignTypeId);

    return !!campaignType ? campaignType.defaultRate: null;
  }

  getDefaultRouteId(campaignTypeId: number): string {
    return "-" + campaignTypeId;
  }

  getOrder(campaignTypeId: number): number {
    var campaignType = this.campaignTypes.find(campaignType => campaignType.id == campaignTypeId);

    return !!campaignType ? campaignType.order : 0;
  }

  isSocialMediaCampaign(campaignTypeId: number) {
    switch (campaignTypeId) {
      case 2:
      case 3:
      case 4:
        return true;
      default:
        return false;
    }
  }

  isRouteCampaign(campaignTypeId: number) {
    switch (campaignTypeId) {
      case 7:
      case 8:
        return true;
      default:
        return false;
    }
  }

  isLinkCampaign(campaignTypeId: number) {
    return campaignTypeId == 1;
  }

  isPromoterQrCampaign(campaignTypeId: number) {
    return campaignTypeId == 5;
  }

  isHandbillCampaign(campaignTypeId: number) {
    return campaignTypeId == 7;
  }

  isPosterCampaign(campaignTypeId: number) {
    return campaignTypeId == 8;
  }
}
