// External
import { Component } from '@angular/core';
import { MbscEventcalendarOptions } from '@mobiscroll/angular';
import { faList, faCalendarAlt } from '@fortawesome/free-solid-svg-icons';


// Internal
import { EventObject } from '../event.models/event-object.model';
import { EventService } from '../event.service';
import { EventProvider } from '../event.provider';
import { SortPipe } from '../../pipes/sort.pipe';


@Component({
  selector: 'event-list',
  templateUrl: './event-list.component.html',
  styleUrls: ['./event-list.component.css'],
  providers: [SortPipe]
})

export class EventList {
  public selectedEvent: EventObject;
  public loading = false;

  events: object[];
  sortedEvents: object[];
  
  searchQuery: string = ''
  orderedBy: string = 'upcoming'

  startDate: string
  endDate: string

  faList = faList
  faCalendarAlt = faCalendarAlt
  toggleView: string = 'list'

  eventSettings: MbscEventcalendarOptions = {
    theme: 'ios',
    display: 'inline',
    view: {
      calendar: {
        type: 'month',
        size: 1,
        labels: true,
        popover: true
      },
      eventList: {
        scrollable: true
      }
    },
  };

  constructor(private eventService: EventService, private eventProvider: EventProvider, private sortPipe: SortPipe) {
    var self = this;
    this.events = [];
    this.sortedEvents = []

    this.getEvents();

    var subscription = eventProvider.selectedEvent.subscribe(
      function (x: EventObject) {
        self.selectedEvent = x;
      },
      function (err) {
        console.log('Error: ' + err);
      },
      function () {
        console.log('Completed');
      }
    );

    let searchSubscription = eventProvider.searchSubject.subscribe((query) => {
      this.searchQuery = query.searchString
      this.toggleView = query.showListView
    })

    let sortSubscription = eventProvider.sortSubject.subscribe((sortOrder) => {
      this.orderedBy = sortOrder
      let sortedEvents = this.sortPipeFn(this.events, this.orderedBy)
      this.updateEventList(sortedEvents)
    })

    let customDateSubscription = eventProvider.customDateSubject.subscribe((dateRange) => {
      this.startDate = dateRange.startDate;
      this.endDate = dateRange.endDate;
      this.orderedBy = dateRange.orderedBy
      let sortedEvents = this.sortPipeFn(this.events, this.orderedBy, +this.startDate, +this.endDate)
      this.updateEventList(sortedEvents)
    })
  }

  sortPipeFn(value: any, orderedBy: any, startDate?: number, endDate?: number) {
    return this.sortPipe.transform(value, orderedBy, startDate, endDate)
  }

  updateEventList(sortedEvents) {
    this.sortedEvents = []
    sortedEvents.forEach((event) => this.sortedEvents.push(event))
  }

  changeView(view: string) {
    this.toggleView = view
  }

  getEvents() {
    this.loading = true;
    this.eventService.getEvents().subscribe(
      (res) => {
        this.events = res.map(event => {
          return {
            ...event,
            d: new Date(event.startTime),
            start: new Date(event.startTime),
            end: new Date(event.endTime),
            text: event.name,
            color: '#ddffff'
          }
        });

        this.sortedEvents = this.sortPipeFn(this.events, this.orderedBy)
        this.loading = false;
      },
      error => {
        console.log(error);
        if (error.indexOf("Error Code: 401;") >= 0) {
          // Unauthorized
          // This can occur if a call to Eventbrite (on a user's behalf) failed with Unauthorized (if their token is no longer valid somehow)
          // TODO: Replace window.alert with a toast, to indicate: "Profile -> Ticket Providers -> Disable Eventbrite -> Enable Eventbrite"
          window.alert("Could not load Events from Eventbrite.\nProfile -> Ticket Providers -> Disable Eventbrite -> Enable Eventbrite");
          // TODO: Do not show "Please select an event from the list on the left"
        }
      });
  }
  
  selectEvent(eventDetails) {
    this.eventProvider.selectEvent(eventDetails);
  }
}
