// External
import { Component, OnInit, ChangeDetectorRef, NgZone, ApplicationRef, ChangeDetectionStrategy } from '@angular/core';
import { Observable, BehaviorSubject, interval, pipe } from 'rxjs';
import { switchMap, map } from 'rxjs/operators';

// Internal
import { PromoterTicketSales } from '../../promoter/promoter.models/promoter-ticket-sales.model';
import { AnalyticsService } from '../../analytics/analytics.service';
import { AppComponent } from '../../app.component';
import { EventObject } from '../event.models/event-object.model';
import { EventService } from '../event.service';
import { EventProvider } from '../event.provider';

@Component({
  selector: 'event-info',
  templateUrl: './event-info.component.html',
  styleUrls: ['./event-info.component.css']
})

export class EventInfo {
  public selectedEvent: EventObject;
  rate: number;

  //promoter vars...clean this up later
  promoterTicketSales: PromoterTicketSales[];
  public totalCostPromoters: number;

  constructor(private analyticsService: AnalyticsService, eventService: EventService, public eventProvider: EventProvider) {
    var self = this;

    var subscription = eventProvider.selectedEvent.subscribe(
      function (x: EventObject) {
        self.selectedEvent = x;
      },
      function (err) {
        console.log('Error: ' + err);
      },
      function () {
        console.log('Completed');
      }
    );
  }

  ngOnInit() {
  }

  close() {
    this.eventProvider.selectEvent(null);
  }

  enableEvent() {
    this.eventProvider.enableEvent(this.selectedEvent.id, this.rate);

    this.rate = null;
  }

  getPromoterTicketSales(eventId: string) {
    this.analyticsService.getPromoterTicketSales(eventId).subscribe((res) => {
      this.promoterTicketSales = res;

      this.totalCostPromoters = 0;

      this.promoterTicketSales.forEach((promoterTicketSale) => {
        this.totalCostPromoters += promoterTicketSale.totalCost;
      });
    });
  }
  /*
  updateSales() {
    this.eventProvider.updateSales(this.selectedEvent.id);
  }
  */
}
