// External
import { Component } from '@angular/core';

// Internal
import { PromoterTicketSales } from '../promoter.models/promoter-ticket-sales.model';
import { AnalyticsService } from '../../analytics/analytics.service';
import { EventProvider } from '../../event/event.provider';
import { EventObject } from '../../event/event.models/event-object.model';
import { CampaignProvider } from '../../campaign/campaign.provider';

@Component({
  selector: 'promoter-list',
  templateUrl: './promoter-list.component.html',
  styleUrls: ['./promoter-list.component.css']
})

export class PromoterList {
  promoterTicketSales: PromoterTicketSales[] = [];
  public selectedEvent: EventObject;
  public totalCostPromoters: number;

  constructor(
    public campaignProvider: CampaignProvider,
    private analyticsService: AnalyticsService,
    private eventProvider: EventProvider) {
    var self = this;
    this.promoterTicketSales = [];
    this.selectEvent(eventProvider.selectedEventObject);

    var subscription = eventProvider.selectedEvent.subscribe(
      function (x: EventObject) {
        self.selectEvent(x);
      },
      function (err) {
        console.log('Error: ' + err);
      },
      function () {
        console.log('Completed');
      }
    );
  }

  selectEvent(eventObject) {
    this.selectedEvent = eventObject;
    this.promoterTicketSales = [];

    if (this.selectedEvent != null) {
      this.getPromoterTicketSales(this.selectedEvent.id);
    }
  }

  getPromoterTicketSales(eventId: string) {
    this.analyticsService.getPromoterTicketSales(eventId).subscribe((res) => {
      this.promoterTicketSales = res;
      console.log(this.promoterTicketSales);

      this.totalCostPromoters = 0;

      this.promoterTicketSales.forEach((promoterTicketSale) => {
        this.totalCostPromoters += promoterTicketSale.totalCost;
      });
    });
  }

  openPromoterDialog(promoterId) {
    let promoterTicketSales = this.promoterTicketSales.find(promoter => promoter.promoterId === promoterId)
    let dialogRef = this.campaignProvider.openPromoterDialog(promoterTicketSales)
  }
}
