/// <reference path="../../../../../Angular/node_modules/@types/googlemaps/index.d.ts" />
// External
import { Component, Inject, ElementRef, NgZone, ViewChild, ViewEncapsulation, Renderer2 } from '@angular/core';
import { MatDialogRef, MatSelectChange, MAT_DIALOG_DATA } from '@angular/material';
import { } from 'googlemaps';
import { CdkTextareaAutosize } from '@angular/cdk/text-field';
import { take } from 'rxjs/operators';
import { ajax } from 'rxjs/ajax';

// Internal
import { CampaignObject } from '../campaign.models/campaign-object.model';
import { EventObject } from '../../event/event.models/event-object.model';
import { CampaignService } from '../campaign.service';
import { CampaignType } from '../campaign.models/campaign-type.model';
import { CampaignTypeProvider } from '../campaign-type.provider';
import { CampaignDetailsMapProvider } from './campaign-details-map.provider';
import { CampaignRoute } from '../campaign.models/campaign-route.model';
import { CampaignRouteMarker } from '../campaign.models/campaign-route-marker.model';
import { EventProvider } from '../../event/event.provider';

@Component({
  selector: 'campaign-details.dialog',
  templateUrl: 'campaign-details.dialog.html',
  styleUrls: ['campaign-details.dialog.css'],
  encapsulation: ViewEncapsulation.None
})

export class CampaignDetailsDialog {
  @ViewChild('autosize') autosize: CdkTextareaAutosize;
  @ViewChild('gmap') gmapElement: ElementRef;
  @ViewChild('locationMap') locationMapElement: ElementRef;
  @ViewChild('routeName') routeNameElement: ElementRef;
  @ViewChild('homeButton') homeButton: ElementRef;
  @ViewChild('routeSelect') routeSelect;
  @ViewChild('locationPacInput') locationPacInput: ElementRef;
  @ViewChild('posterPacInput') posterPacInput: ElementRef;

  public map: google.maps.Map;
  public campaign: CampaignObject;
  public eventObject: EventObject;
  public renamingRoute: boolean = false;
  public campaignTypes: CampaignType[] = new Array();
  public campaignRoutes: CampaignRoute[] = new Array();
  public filteredCampaignRoutes: CampaignRoute[] = new Array();
  public dialog = this;
  public newCampaignRouteZoom: number = 15;
  public isEdit: boolean = false;
  public isBack: boolean = false;
  public loading: boolean = false;
  public autocompleteListenerAdded: boolean = false
  public pacEventListener: Function
  public base64textString: string = "";
  public markerAdded: boolean = false
  public promotersCount: number = 0;

  constructor(
    public renderer: Renderer2,
    public dialogRef: MatDialogRef<CampaignDetailsDialog>,
    private campaignService: CampaignService,
    private campaignTypeProvider: CampaignTypeProvider,
    private campaignDetailsMapProvider: CampaignDetailsMapProvider,
    private eventProvider: EventProvider,
    private ngZone: NgZone,
    @Inject(MAT_DIALOG_DATA) public data
  ) {
    this.isEdit = data.isEdit;
    this.campaign = data.campaign;
    this.eventObject = data.eventObject;
    this.isBack = data.isBack
  }

  // Dialog Methods
  ngOnInit() {
    ajax('/api/Venues/Promoters/').subscribe(res => {
      this.promotersCount = res.response ? res.response.length : 0;
    });
    this.campaignTypes = this.campaignTypeProvider.getCampaignTypes();

    if (this.isEdit || this.campaign.campaignTypeId !== 8) {
      this.markerAdded = true
    }

    if (this.isRouteCampaign()) {
      this.loading = true

      // Init map
      let center = new google.maps.LatLng(this.eventObject.latitude, this.eventObject.longitude);

      let mapProp = {
        center: center,
        zoom: this.newCampaignRouteZoom,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
        disableDefaultUI: true
      };

      // Location Map
      if (this.campaign.campaignTypeId === 7) {
        this.map = new google.maps.Map(this.locationMapElement.nativeElement, mapProp)
      }

      // Poster Map
      if (this.campaign.campaignTypeId === 8) {
        this.map = new google.maps.Map(this.gmapElement.nativeElement, mapProp)
      }

      this.campaignDetailsMapProvider.map = this.map;

      // Reset campaignDetailsMapProvider.campaignRouteMarkers
      this.campaignDetailsMapProvider.clearCampaignRouteMarkers();

      // Load CampaignRoutes and insert newHandbillRoute and newPosterRoute
      this.campaignService.getCampaignRoutes(null).subscribe((res) => {
        this.campaignRoutes = res;

        // Location Map
        if (this.campaign.campaignTypeId === 7) {
          let newHandbillRoute = this.createCampaignRoute(center, 7); // TODO: Use const for campaignTypeId_Handbill
          this.campaignRoutes.splice(0, 0, newHandbillRoute);
        }

        // Poster Map
        if (this.campaign.campaignTypeId === 8) {
          let newPosterRoute = this.createCampaignRoute(center, 8); // TODO: Use const for campaignTypeId_Poster
          this.campaignRoutes.splice(1, 0, newPosterRoute);
        }

        // Trigger onCampaignTypeChange (after campaignRoutes have been loaded)
        this.onCampaignTypeChange(null);

      });
    }
  }

  markerIsAdded() {
    this.markerAdded = true
  }

  allMarkersRemoved() {
    this.markerAdded = false
  }

  onCampaignTypeChange(event: MatSelectChange) {
    this.campaign.applyFormFields(this.map);

    if (this.campaignTypeProvider.isRouteCampaign(this.campaign.campaignTypeId)) {
      this.campaignDetailsMapProvider.clearCampaignRouteMarkers();

      if (this.campaign.campaignRouteId == null
        || this.campaign.campaignRouteId == this.campaignTypeProvider.getDefaultRouteId(7) // TODO: Use const
        || this.campaign.campaignRouteId == this.campaignTypeProvider.getDefaultRouteId(8)) { // TODO: Use const
        this.campaign.campaignRouteId = this.campaignTypeProvider.getDefaultRouteId(this.campaign.campaignTypeId);
      }

      this.filteredCampaignRoutes = this.getCampaignRoutes(this.campaign.campaignTypeId);

      this.campaignRouteSelected(this.campaign.campaignRouteId, true);
    }
  }

  campaignRouteSelected(campaignRouteId, copy: boolean) {
    let campaignRoute = this.campaignRoutes.find(campaignRoute => campaignRoute.id == campaignRouteId);

    if (!!campaignRoute) {
      this.campaign.campaignRoute = campaignRoute;

      // TODO: NEED TO FIX copy!!! Will currently modify any Route that is already in progress and/or invalidate all promoter data if modified.
      //this.campaign.formFields.campaignRouteId = copy ? this.campaignTypeProvider.getDefaultRouteId(this.campaign.campaignTypeId) : campaignRoute.id;
      this.campaign.formFields.campaignRouteId = campaignRoute.id;
      this.campaign.formFields.routeName = campaignRoute.name;
      this.campaign.campaignRoute.map = this.map;

      let locationCircleMarker = this.campaignDetailsMapProvider.getLocationCircle()

      let center = new google.maps.LatLng(campaignRoute.latitude, campaignRoute.longitude);

      if (this.campaign.campaignTypeId === 7 && locationCircleMarker) {
        center = new google.maps.LatLng(locationCircleMarker.lat, locationCircleMarker.lng)
      }

      this.map.setCenter(center);
      //this.map.setZoom(campaignRoute.zoom);

      this.campaignDetailsMapProvider.clearCampaignRouteMarkers();

      let bounds = new google.maps.LatLngBounds()

      campaignRoute.campaignRouteMarkers.forEach((campaignRouteMarker) => {
        campaignRouteMarker.label = this.campaignDetailsMapProvider.nextLabel();

        if (this.campaign.campaignTypeId === 7 && locationCircleMarker) {
          campaignRouteMarker.latitude = locationCircleMarker.lat
          campaignRouteMarker.longitude = locationCircleMarker.lng
        }

        this.campaignDetailsMapProvider.addMarker(this.map, campaignRouteMarker);

        // POSTER-ROUTE
        if (this.campaign.campaignTypeId === 8 && this.isEdit && campaignRoute.campaignRouteMarkers.length > 1) {
          bounds.extend({ lat: campaignRouteMarker.latitude, lng: campaignRouteMarker.longitude })
          this.map.fitBounds(bounds)
        }
      })
    }

    this.filteredCampaignRoutes = this.getCampaignRoutes(this.campaign.campaignTypeId);
    this.loading = false
  }

  triggerResize() {
    // Wait for changes to be applied, then trigger textarea resize.
    this.ngZone.onStable.pipe(take(1))
      .subscribe(() => this.autosize.resizeToFitContent(true));
  }

  getNewCampaignRouteName(campaignTypeId: number) {
    let campaignTypeName = this.campaignTypeProvider.getCampaignTypeName(campaignTypeId);
    let index = "1"; // TODO: Increment based on the count of existing routes or routes of campaignTypeId
    let campaignRouteName = this.eventObject.name + " - " + campaignTypeName + " Route " + index

    return campaignRouteName;
  }

  createCampaignRoute(center: google.maps.LatLng, campaignTypeId: number): CampaignRoute {
    let campaignRoute = new CampaignRoute({
      id: this.campaignTypeProvider.getDefaultRouteId(campaignTypeId),
      name: this.getNewCampaignRouteName(campaignTypeId),
      campaignTypeId: campaignTypeId,
      latitude: center.lat(),
      longitude: center.lng(),
      zoom: this.newCampaignRouteZoom
    });

    if (campaignTypeId == 7) { // TODO: Use const for campaignTypeID_Handbill
      let campaignRouteMarker = new CampaignRouteMarker({
        typeId: 2, // TODO: Use const for typeId 2?
        map: this.map,
        latitude: center.lat(),
        longitude: center.lng(),
        radius: this.campaignDetailsMapProvider.circleRadiusVenue
      });

      campaignRoute.campaignRouteMarkers.push(campaignRouteMarker);
    }

    return campaignRoute;
  }

  onCancel(): void {
    this.dialogRef.close({
      nav: 'cancel',
      data: {
        campaign: this.campaign,
        eventObj: this.eventObject
      }
    })
  }

  onBack(): void {
    this.dialogRef.close({
      nav: 'back',
      data: {
        campaign: this.campaign,
        eventObj: this.eventObject
      }
    })
  }

  onNext(): void {
    this.dialogRef.close({
      nav: 'next',
      data: {
        campaign: this.campaign,
        eventObj: this.eventObject,
      }
    })
  }

  // Event Handlers - Common
  copyCampaignId() {
    this.copyMessage(this.campaign.id);
  }

  copyMessage(val: string) {
    let selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = val;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
  }

  getCampaignRoutes(campaignTypeId: number): Array<CampaignRoute> {
    let campaignRoutes = this.campaignRoutes.filter((campaignRoute) => {
      return campaignRoute.campaignTypeId === campaignTypeId || campaignRoute.campaignTypeId === 0;
    });

    return campaignRoutes;
  }

  showSnackbar(message: string) {
    // Get the snackbar DIV
    let x = document.getElementById("snackbar");

    // Add the "show" class to DIV
    x.className = "show";
    x.innerText = message;

    // After 3 seconds, remove the show class from DIV
    setTimeout(function () { x.className = x.className.replace("show", ""); }, 3000);
  }

  deleteCampaign(id) {
    this.campaignService.deleteCampaign(id).subscribe((deleteCampaignResult) => {
      this.campaignService.change.emit(id);
      this.showSnackbar("Campaign Deleted");
    }, (errorResult) => {
      console.log(errorResult);
      this.showSnackbar("Failed to delete Campaign");
    });
    this.onCancel();
  }

  renameRoute() {
    this.renamingRoute = !this.renamingRoute;

    if (this.renamingRoute) {
      // Start Rename
      this.routeNameElement.nativeElement.focus();

      setTimeout(() => {
        this.routeNameElement.nativeElement.focus();
      }, 0);
    }
    else {
      // Save Name
      let campaignRoute = this.campaignRoutes.find(campaignRoute => campaignRoute.id == this.campaign.formFields.campaignRouteId);
      campaignRoute.name = this.campaign.formFields.routeName;
    }
  }

  onRouteNameKeyEnter(event: any) {
    this.renameRoute(); // TODO: Force to false (shouldn't matter, but would be nice to have the option)
  }

  onCampaignRouteChange(event: MatSelectChange) {
    let campaignRouteId = event.value;
    this.campaignRouteSelected(campaignRouteId, true);
  }

  // Event Handlers - Times/Dates/Days
  onTimePeriodChange(event: MatSelectChange) {
    this.campaign.applyTimePeriod(event.value);
  }

  onDateRangeChange(event: MatSelectChange) {
    this.campaign.applyDateRange(event.value);
  }

  isSocialMediaCampaign() {
    return this.campaignTypeProvider.isSocialMediaCampaign(this.campaign.campaignTypeId);
  }

  isRouteCampaign() {
    return this.campaignTypeProvider.isRouteCampaign(this.campaign.campaignTypeId);
  }

  isHandbillCampaign() {
    return this.campaignTypeProvider.isHandbillCampaign(this.campaign.campaignTypeId);
  }

  isPosterCampaign() {
    return this.campaignTypeProvider.isPosterCampaign(this.campaign.campaignTypeId);
  }

  isLinkCampaign() {
    return this.campaignTypeProvider.isLinkCampaign(this.campaign.campaignTypeId);
  }

  isPromoterQrCampaign() {
    return this.campaignTypeProvider.isPromoterQrCampaign(this.campaign.campaignTypeId);
  }

  isEditCampaign() {
    return this.isEdit;
  }

  isMarkerAdded() {
    return this.markerAdded
  }

  // Event Handlers - Map - Header Button Handlers
  addCircle() {
    let center = this.map.getCenter();
    this.campaignDetailsMapProvider.addCircleMarker(this.campaign.campaignRoute.map, center, this.campaignDetailsMapProvider.circleRadiusAdd);
  }

  deleteCircle() {
    this.campaignDetailsMapProvider.deleteSelectedMarker();
  }

  addLocation() {
    let center = this.map.getCenter();
    this.campaignDetailsMapProvider.addLocationMarker(this.campaign.campaignRoute.map, center, this.campaignDetailsMapProvider.nextLabel(), "", null);
    this.markerIsAdded()
  }

  deleteLocation() {
    this.campaignDetailsMapProvider.deleteSelectedMarker();
    let markers = this.campaignDetailsMapProvider.getCampaignRouteMarkers()

    if (markers.length === 0) {
      this.allMarkersRemoved()
    }
  }

  importRoute() {
    this.routeSelect.open();
  }

  centerHome() {
    let center = new google.maps.LatLng(this.eventObject.latitude, this.eventObject.longitude);
    this.map.setCenter(center);
  }

  // Event Handlers - Map - Search
  onSearchKey(event: any) {

    if (!this.autocompleteListenerAdded) {
      this.pacEventListener = this.renderer.listen('body', 'mousedown', this.onClickSearchResult.bind(this))
      this.autocompleteListenerAdded = true
    }

    if (event.key == "Enter") {
      var criteria = event.target.value;
      this.searchPlaces(criteria)
      this.markerIsAdded()
    }
  }

  onClickSearchResult(event) {
    if (event.target.parentNode.className.includes('pac')) {
      let criteria = event.path.find(node => node.className === 'pac-item').textContent
      this.searchPlaces(criteria)
      this.markerIsAdded()
    }
  }

  searchPlaces(criteria) {
    let location = new google.maps.LatLng(this.eventObject.latitude, this.eventObject.longitude);
    this.campaignDetailsMapProvider.searchPlaces(this.campaign.campaignRoute.map, criteria, location, this.campaign.campaignTypeId);
    this.homeButton.nativeElement.focus();
  }

  ngOnDestroy() {
    // remove pacEventListener onDestroy
    if (this.pacEventListener) {
      this.pacEventListener()
    }

    this.campaignDetailsMapProvider.resetPrevLocation()
  }

  //convert instagram image to base64
  handleFileSelect(evt) {
    var files = evt.target.files;
    var file = files[0];

    if (files && file) {
      var reader = new FileReader();
      reader.onload = this.handleReaderLoaded.bind(this);
      reader.readAsBinaryString(file);
    }
  }

  handleReaderLoaded(readerEvt) {
    var binaryString = readerEvt.target.result;
    this.base64textString = btoa(binaryString);
    this.campaignService.passBase64(this.base64textString);
  }

  submitInstagramImage() {
    this.campaignService.uploadImage(this.campaign.id, this.base64textString)
  }

  markCampaignComplete() {
    if (this.campaign.formFields.campaignTypeName === 'Poster') {
      this.campaignService.campaignPosterRouteStatusComplete(this.campaign.id,
        {
          status: 3
        })
        .subscribe(() => {
          this.eventProvider.selectEvent(this.eventObject);
          this.onCancel();
        });
    } else if (this.campaign.formFields.campaignTypeName === 'Location') {
      this.campaignService.campaignHandbillRouteStatusComplete(this.campaign.id,
        {
          status: 3,
          duration: this.campaign.formFields.duration
        })
        .subscribe(() => {
          this.eventProvider.selectEvent(this.eventObject);
          this.onCancel();
        });
    }
  }

  isCampaignClaimed() {
    if (this.campaign.formFields.campaignTypeName === 'Poster') {
      if (this.campaign['posterRouteStatus']) {
        return this.campaign['posterRouteStatus']['posterRouteStatusId'] === 1;
      }
    } else if (this.campaign.formFields.campaignTypeName === 'Location') {
      if (this.campaign['handbillStatus']) {
        return this.campaign['handbillStatus']['handbillStatusId'] === 1;
      }
    }
    return false;
  }

  isCampaignStarted() {
    if (this.campaign.formFields.campaignTypeName === 'Location') {
      if (this.campaign['handbillStatus']) {
        return this.campaign['handbillStatus']['handbillStatusId'] === 2;
      }
    }
    return false;
  }

  isCampaignDefault() {
    if (
      (this.campaign['posterRouteStatus'] === null || this.campaign['posterRouteStatus'] === undefined)
      && (this.campaign['handbillStatus'] === null || this.campaign['handbillStatus'] === undefined)
    ) {
      return true;
    }
    if (this.campaign.formFields.campaignTypeName === 'Poster') {
      if (this.campaign['posterRouteStatus']) {
        return this.campaign['posterRouteStatus']['posterRouteStatusId'] === 0;
      }
    } else if (this.campaign.formFields.campaignTypeName === 'Location') {
      if (this.campaign['handbillStatus']) {
        return this.campaign['handbillStatus']['handbillStatusId'] === 0;
      }
    }
    return true;
  }

}
