// External
import { Component, Input } from '@angular/core';

// Internal
import { AppComponent } from '../../app.component';
import { CampaignProvider } from '../campaign.provider';
import { CampaignRouteMarker } from '../campaign.models/campaign-route-marker.model';
import { CampaignDetailsMapProvider } from '../campaign-details.dialog/campaign-details-map.provider';

@Component({
  selector: 'campaign-route-marker-list',
  templateUrl: './campaign-route-marker-list.component.html',
  styleUrls: ['./campaign-route-marker-list.component.css']
})

export class CampaignRouteMarkerList {

  @Input('onCampaignDefault') onCampaignDefault:boolean;
  
  campaignRouteMarkers: CampaignRouteMarker[];
  showPoster: boolean = false;
  focusCampaignRouteMarker: CampaignRouteMarker;

  constructor(private campaignProvider: CampaignProvider, private campaignDetailsMapProvider: CampaignDetailsMapProvider) {
    var self = this;

    this.campaignRouteMarkers = [];
    
    var subscription = campaignDetailsMapProvider.campaignRouteMarkersPublisher.subscribe(
      function (campaignRouteMarkers: CampaignRouteMarker[]) {
        self.campaignRouteMarkers = campaignRouteMarkers;
      },
      function (err) {
        console.log('Error: ' + err);
      },
      function () {
        console.log('Completed');
      }
    );    
  }

  onMouseEnter(event, campaignRouteMarker) {
    // TODO: Set posterUrl and timestamp
    this.showPoster = true;
    this.focusCampaignRouteMarker = campaignRouteMarker;
  }

  onMouseLeave(event, campaignRouteMarker) {
    this.showPoster = false;
  }

  ngOnInit() {

  }
}
