// Internal
import { CampaignTicketSales } from './campaign-ticket-sales.model';
import { TicketClassSales } from './ticket-class-sales.model';
import { CampaignTypeProvider } from '../../campaign/campaign-type.provider';

export class PromoterTicketSales {
  promoterId: string;
  promoterName: string;
  promoterEmail: string;
  promoterIcon: string;

  campaignTicketSales: CampaignTicketSales[];
  totalEarned: number;

  ticketClassSales: TicketClassSales[];
  totalCost: number;

  constructor(values: Object = {}, campaignTypeProvider: CampaignTypeProvider) {
    Object.assign(this, values);

    // Hydrate campaignTicketSales with UI related fields, not included in the API response
    this.campaignTicketSales.forEach((campaignTicketSale: CampaignTicketSales) => {
      campaignTicketSale.campaignTypeIcon = campaignTypeProvider.getIcon(campaignTicketSale.campaignType);
    });
  }
}
