// External
import { Component, ViewChild, ElementRef } from '@angular/core';
import { faSlidersH, faSearch, faTimes, faPlus } from '@fortawesome/free-solid-svg-icons';
import { fromEvent } from 'rxjs';
import { ajax } from 'rxjs/ajax';
import { debounceTime, distinctUntilChanged, filter, map, switchMap } from 'rxjs/operators';

// Internal
import { EventProvider } from '../../event/event.provider';
import { EventObject } from '../../event/event.models/event-object.model';
import { text } from '@angular/core/src/render3/instructions';
import { Promoter } from '../../promoter/promoter.models/promoter.model';
import { PromoterService } from '../../promoter/promoter.service';
import { YourPromotersService } from '../your-promoters.service';

@Component({
    selector: 'your-promoters',
    templateUrl: './your-promoters.component.html',
    styleUrls: ['./your-promoters.component.css']
})

export class YourPromoters {
    //FONT-AWESOME-ICONS
    faSlidersH = faSlidersH
    faSearch = faSearch
    faTimes = faTimes
    faPlus = faPlus

    //SEARCH
    search: any;

    @ViewChild('searchbox') searchBox: ElementRef;
    promoters: Promoter[];
    searchPromoters: Promoter[];

    constructor(private promoterService: PromoterService, public yourPromoterService: YourPromotersService, private eventProvider: EventProvider) {
        this.promoters = [];
        this.searchPromoters = [];
    }

    ngAfterViewInit() {
        // initialize typeahead
        const typeahead = fromEvent(this.searchBox.nativeElement, 'input').pipe(
            map((e: KeyboardEvent) => (e.target as HTMLInputElement).value),
            debounceTime(10),
            distinctUntilChanged(),
            switchMap((text) => this.yourPromoterService.searchPreferredPromoter(text ? text : '$'))
        );

        typeahead.subscribe(res => {
            this.searchPromoters = res.json() ? res.json() : [];
        });
        
        // populate preferred promoters list
        ajax('/api/Venues/Promoters/').subscribe(res => {
            this.promoters = res.response;
        });
    }

    clearSearch() {
      this.search = '';
    }

    addPromoter(promoter) {
        this.yourPromoterService.addPreferredPromoter(promoter.id).subscribe(res => {
            this.searchBox.nativeElement.value = '';
            this.searchPromoters = [];
            this.promoters.push(promoter);
        });
    }

    removePromoter(promoterRemove) {
        this.yourPromoterService.removePreferredPromoter(promoterRemove.id).subscribe(res => {

            this.promoters = this.promoters.filter((promoter) => {
                return promoter.id !== promoterRemove.id;
            });
        });
    }

    isPreferredPromoter(promoterId) {
        return this.promoters.find((promoter) => {
            return promoter.id === promoterId;
        });
    }
}
