  getSalesByDate(eventId: string) {
    this.analyticsService.getSalesByDate(eventId).subscribe((res) => {
      if (res.length > 0) {
        this.hasSales = true;

        var salesByDate = new Array();
        res.forEach((saleByDate) => {
          var date = (new Date(saleByDate.date)).getTime();

          salesByDate.push([+date, saleByDate.sales]);
        });

        salesByDate = salesByDate.sort((n1, n2) => n1[0] - n2[0]);
        this.salesByDateChart = this.createChartSalesByDate(salesByDate);
      }
    });
  }

  getSalesByAge(eventId: string) {
    this.analyticsService.getSalesByAge(eventId).subscribe((res) => {
      var salesByAge = new Array();
      if (res.length > 0) {
        this.hasSales = true;

        res.forEach((saleByAge) => {
          var count = saleByAge.age;

          salesByAge.push([+count, saleByAge.sales]);
        });

        salesByAge = salesByAge.sort((n1, n2) => n1[0] - n2[0]);
        this.salesByAgeChart = this.createChartSalesByAge(salesByAge);
      } else {
        salesByAge = null;
        this.salesByAgeChart = this.createChartSalesByAge(salesByAge);
      }
    });
  }

