import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { PromoterTicketSales } from '../promoter.models/promoter-ticket-sales.model';
import { PromoterService } from '../promoter.service';
import { faArrowUp, faArrowDown } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-promoter-dialog',
  templateUrl: './promoter-dialog.component.html',
  styleUrls: ['./promoter-dialog.component.css']
})

export class PromoterDialogComponent implements OnInit {
  promoterTicketSales: PromoterTicketSales[] = [];
    promoterId: string = null;

    faArrowUp = faArrowUp;
    faArrowDown = faArrowDown;

    promoterProfilePic: any = null;
    facebookFriendsCount: string = null;
    facebookAveragePostReactions: string = null;
    facebookAveragePostComments: string = null;
    facebookAveragePostShares: string = null;

    instaProfilePic: string = null;
    instaFollowersCount: string = null;
    instaHandle: string = null;
    instaWebsiteInBio: string = null;
    instaTotalPosts: string = null;

    instaAccountImpressions: string = null;
    instaAccountReach: string = null;

    instaAccountProfileViewsYesterday: number = null;
    instaAccountProfileViewsToday: number = null;
    instaProfileViewDifference: number = null;
    //0 = no change between today and yesterday, 1 = higher today vs yesterday, 2 = lower today vs yesterday
    instaProfileViewHigher: number = null;

    instaWebsiteClicksYesterday: number = null;
    instaWebsiteClicksToday: number = null;
    instaWebsiteClickDifference: number = null;
    //0 = no change between today and yesterday, 1 = higher today vs yesterday, 2 = lower today vs yesterday
    instaWebsiteClickHigher: number = null;

    instaAveragePostLikes: string = null;
    instaAveragePostComments: string = null;
    instaAveragePostSaves: string = null;

    toggleView: string = 'facebook';

  constructor(
    public dialogRef: MatDialogRef<PromoterDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private promoterService: PromoterService) {

      this.promoterTicketSales = data;

      this.promoterId = data.promoterId;

      this.getSocialMediaData();
  }

  changeView(view: string) {
      this.toggleView = view
  }

  ngOnInit() {
  }

  getSocialMediaData() {
    this.promoterService.getPromoterSocialDetails(this.promoterId, null).subscribe((res) => {
        for (let i = 0; i < res.length; i++) {
            if (res[i].claimType == "Facebook.ProfilePicture") {
                this.promoterProfilePic = res[i].claimValue;
            }
            switch (res[i].claimType) {
                case "Facebook.ProfilePicture":
                    this.promoterProfilePic = res[i].claimValue;
                    break;
                case "Facebook.AveragePostReactions":
                    this.facebookAveragePostReactions = res[i].claimValue;
                    break;
                case "Facebook.AveragePostComments":
                    this.facebookAveragePostComments = res[i].claimValue;
                    break;
                case "Facebook.AveragePostShares":
                    this.facebookAveragePostShares = res[i].claimValue;
                    break;
                case "Facebook.FriendsCount":
                    this.facebookFriendsCount = res[i].claimValue;
                    break;
                case "Instagram.ProfilePicture":
                    this.instaProfilePic = res[i].claimValue;
                    break;
                case "Instagram.FollowersCount":
                    this.instaFollowersCount = res[i].claimValue;
                    break;
                case "Instagram.Handle":
                    this.instaHandle = res[i].claimValue;
                    break;
                case "Instagram.WebsiteInBio":
                    this.instaWebsiteInBio = res[i].claimValue;
                    break;
                case "Instagram.TotalPosts":
                    this.instaTotalPosts = res[i].claimValue;
                    break;
                case "Instagram.AccountImpressions":
                    this.instaAccountImpressions = res[i].claimValue;
                    break;
                case "Instagram.AccountReach":
                    this.instaAccountReach = res[i].claimValue;
                    break;
                case "Instagram.AccountProfileViewsYesterday":
                    this.instaAccountProfileViewsYesterday = Number(res[i].claimValue);
                    break;
                case "Instagram.AccountProfileViewsToday":
                    this.instaAccountProfileViewsToday = Number(res[i].claimValue);
                    this.getDailyProfileViewDifference();
                    break;
                case "Instagram.WebsiteClicksYesterday":
                    this.instaWebsiteClicksYesterday = Number(res[i].claimValue);
                    break;
                case "Instagram.WebsiteClicksToday":
                    this.instaWebsiteClicksToday = Number(res[i].claimValue);
                    this.getDailyWebClickDifference();
                    break;
                case "Instagram.AveragePostLikes":
                    this.instaAveragePostLikes = res[i].claimValue;
                    break;
                case "Instagram.AveragePostComments":
                    this.instaAveragePostComments = res[i].claimValue;
                    break;
                case "Instagram.AveragePostSaves":
                    this.instaAveragePostSaves = res[i].claimValue;
                    break;
                default:
                    console.log("No social media data exists");
                    break;
            }
        }
        //if(res["claimType"] == "Facebook.")
    })
  }

    getDailyWebClickDifference() {
        if (this.instaWebsiteClicksToday != null && this.instaWebsiteClicksYesterday != null) {
            if (this.instaWebsiteClicksToday > this.instaWebsiteClicksYesterday) {
                this.instaWebsiteClickDifference = (this.instaWebsiteClicksToday - this.instaWebsiteClicksYesterday);
                this.instaWebsiteClickHigher = 1;
            } else if (this.instaWebsiteClicksToday < this.instaWebsiteClicksYesterday) {
                this.instaWebsiteClickDifference = (this.instaWebsiteClicksYesterday - this.instaWebsiteClicksToday);
                this.instaWebsiteClickHigher = 2;
            } else if (this.instaWebsiteClicksToday == this.instaWebsiteClicksYesterday) {
                this.instaWebsiteClickDifference = 0;
                this.instaWebsiteClickHigher = 0;
            } else {
                console.log("error calculating click difference");
            }
        }
        
    }

    getDailyProfileViewDifference() {
        if (this.instaAccountProfileViewsToday != null && this.instaAccountProfileViewsYesterday != null) {
            console.log("Today: " + this.instaAccountProfileViewsToday + ", Yesterday: " + this.instaAccountProfileViewsYesterday);
            if (this.instaAccountProfileViewsToday > this.instaAccountProfileViewsYesterday) {
                this.instaProfileViewDifference = (this.instaAccountProfileViewsToday - this.instaAccountProfileViewsYesterday);
                this.instaProfileViewHigher = 1;
            } else if (this.instaAccountProfileViewsToday < this.instaAccountProfileViewsYesterday) {
                this.instaProfileViewDifference = (this.instaAccountProfileViewsYesterday - this.instaAccountProfileViewsToday);
                this.instaProfileViewHigher = 2;
            } else if (this.instaAccountProfileViewsToday == this.instaAccountProfileViewsYesterday) {
                this.instaProfileViewDifference = 0;
                this.instaProfileViewHigher = 0;
            } else {
                console.log("There was an error calculating the difference");
            }
        }
    }
}
