// External
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable, pipe, Subject, throwError } from 'rxjs';
import { switchMap, map, catchError } from 'rxjs/operators';
import { EventObject } from './event.models/event-object.model';

@Injectable()
export class EventService {
  private baseUrl: string = "..";
  private show: string = 'all';

  constructor(private http: Http) { }

  handleError(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // client-side error
      errorMessage = `Error: ${error.error.message}`;
    } else {
      // server-side error
      errorMessage = `Error Code: ${error.status}; Message: ${error.message}`;
    }

    return throwError(errorMessage);
  };

  getEvents() {
    let apiUrl = this.baseUrl + '/api/Events';

    return this.http.get(apiUrl).pipe(
      map((res: Response) => {
        let response = res.json()
        return this.sortEvents(response, 'asc', 'startTime')
      }),
      catchError(this.handleError)
    );
  }

  enableEvent(eventId: string, defaultRate: number) {
    let apiUrl = this.baseUrl + '/api/Events/Enable/' + eventId;

    let enableEventRequest = {
      defaultRate: defaultRate
    };

    return this.http.post(apiUrl, enableEventRequest).pipe(
      map((res: Response) => {
        return res;
      })
    );
  }

  updateSales(eventId: string) {
    let apiUrl = this.baseUrl + '/api/Events/UpdateSales/' + eventId;

    return this.http.post(apiUrl, null).pipe(
      map((res: Response) => {
        return res;
      })
    );
  }

  sortEvents(value: EventObject[], order: string = 'asc' || 'desc', orderBy: string) {
    let sortedValues: any[];

    if (order === "asc") {
      sortedValues = value.sort((a, b) => {
        if (a[orderBy] > b[orderBy]) {
          return 1;
        } else if (a[orderBy] < b[orderBy]) {
          return -1;
        } else {
          return 0;
        }
      });
    }

    if (order === "desc") {
      sortedValues = value.sort((a, b) => {
        if (a[orderBy] > b[orderBy]) {
          return -1;
        } else if (a[orderBy] < b[orderBy]) {
          return 1;
        } else {
          return 0;
        }
      });
    }

    return sortedValues;
  }

}
