// External
import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';

// Internal
import { CampaignObject } from './campaign.models/campaign-object.model';
import { EventObject } from '../event/event.models/event-object.model';
import { CampaignService } from './campaign.service';
import { CampaignDetailsDialog } from './campaign-details.dialog/campaign-details.dialog';
import { CampaignTypeProvider } from './campaign-type.provider';
import { CampaignRoute } from './campaign.models/campaign-route.model';
import { CampaignDetailsMapProvider } from './campaign-details.dialog/campaign-details-map.provider';
import { CampaignTypeFormComponent } from './campaign-type-form/campaign-type-form.component';
import { CampaignDateTimeFormComponent } from './campaign-date-time-form/campaign-date-time-form.component';
import { CampaignRateFormComponent } from './campaign-rate-form/campaign-rate-form.component';
import { LocationMapFormComponent } from './location-map-form/location-map-form.component';
import { LocationDateTimeFormComponent } from './location-date-time-form/location-date-time-form.component';
import { PosterMapFormComponent } from './poster-map-form/poster-map-form.component';
import { PosterDateTimeFormComponent } from './poster-date-time-form/poster-date-time-form.component';
import { InstagramPhotoFormComponent } from './instagram-photo-form/instagram-photo-form.component';
import { PromoterDialogComponent } from '../promoter/promoter-dialog/promoter-dialog.component';

@Injectable()
export class CampaignProvider {
  private campaign: CampaignObject;
  isEdit: boolean;

  campaignTypeDialogRef: MatDialogRef<CampaignTypeFormComponent>
  campaignTypeDialogData: { nav: string, data: { campaign: CampaignObject, eventObj: EventObject } }

  dateTimeDialogRef: MatDialogRef<CampaignDateTimeFormComponent>
  dateTimeDialogData: { nav: string, data: { campaign: CampaignObject, eventObj: EventObject } }

  rateDialogRef: MatDialogRef<CampaignRateFormComponent>
  rateDialogData: { nav: string, data: { campaign: CampaignObject, eventObj: EventObject } }

  summaryDialogRef: MatDialogRef<CampaignDetailsDialog>
  summaryDialogData: { nav: string, data: { campaign: CampaignObject, eventObj: EventObject } }

  locationMapDialogRef: MatDialogRef<LocationMapFormComponent>
  locationMapDialogData: { nav: string, data: { campaign: CampaignObject, eventObj: EventObject } }

  locationDateTimeDialogRef: MatDialogRef<LocationDateTimeFormComponent>
  locationDateTimeDialogData: { nav: string, data: { campaign: CampaignObject, eventObj: EventObject } }

  posterMapDialogRef: MatDialogRef<PosterMapFormComponent>
  posterMapDialogData: { nav: string, data: { campaign: CampaignObject, eventObj: EventObject } }

  posterDateTimeDialogRef: MatDialogRef<PosterDateTimeFormComponent>
  posterDateTimeDialogData: { nav: string, data: { campaign: CampaignObject, eventObj: EventObject } }

  linkDialogRef: MatDialogRef<PosterDateTimeFormComponent>
  linkDialogData: { nav: string, data: { campaign: CampaignObject, eventObj: EventObject } }

  qrDialogRef: MatDialogRef<PosterDateTimeFormComponent>
  qrDialogData: { nav: string, data: { campaign: CampaignObject, eventObj: EventObject } }

  instagramPhotoDialogRef: MatDialogRef<InstagramPhotoFormComponent>
  instagramPhotoDialogData: { nav: string, data: { campaign: CampaignObject, eventObj: EventObject } }

  promoterDialogRef: MatDialogRef<PromoterDialogComponent>
  promoterDialogData: { data: any }


  constructor(
    private campaignService: CampaignService,
    private campaignTypeProvider: CampaignTypeProvider,
    public dialog: MatDialog,
    public campaignDetailsMapProvider: CampaignDetailsMapProvider) { }

  openPromoterDialog(promoterTicketSalesData) {
    this.promoterDialogRef = this.dialog.open(PromoterDialogComponent, { data: promoterTicketSalesData })
  }


  

  editDialog(campaign: CampaignObject, eventObject: EventObject): MatDialogRef<CampaignDetailsDialog> {
    this.campaign = campaign;
    this.openSummaryDialog(campaign, eventObject, true)
    return this.summaryDialogRef;
  }

  openCampaignTypeDialog(campaign: CampaignObject, eventObject: EventObject) {
    this.campaign = campaign

    this.campaignTypeDialogRef = this.dialog.open(CampaignTypeFormComponent, {
      data: { campaign: campaign || new CampaignObject({}, this.campaignTypeProvider), eventObject: eventObject }
    });

    this.closeCampaignTypeDialog()
  }

  closeCampaignTypeDialog() {
    // campaignTypeDialog - afterClosed()
    if (this.campaignTypeDialogRef) {
      this.campaignTypeDialogRef.afterClosed().subscribe(campaignTypeData => {
        if (campaignTypeData) {
          this.campaignTypeDialogData = campaignTypeData

          // cancel:
          if (campaignTypeData.nav === 'cancel') {
            return campaignTypeData
          }

          // if (nav = "next" && campaignType === socialMedia) : dateTimeDialog
          if (campaignTypeData.nav === 'next' && campaignTypeData.data.campaign.campaignTypeId !== 7 && campaignTypeData.data.campaign.campaignTypeId !== 8) {
            this.openDateTimeDialog(campaignTypeData.data.campaign, campaignTypeData.data.eventObj)
          }

          // if (nav = "next" && campaignType === poster): campaignRateDialog
          if (campaignTypeData.nav === 'next' && campaignTypeData.data.campaign.campaignTypeId === 8) {
            this.openRateDialog(campaignTypeData.data.campaign, campaignTypeData.data.eventObj)
          }

          // if (nav = "next" && campaignType === location): locationMap
          if (campaignTypeData.nav === 'next' && campaignTypeData.data.campaign.campaignTypeId === 7) {
            this.openLocationMapDialog(campaignTypeData.data.campaign, campaignTypeData.data.eventObj)
          }
        }
      })
    }
  }

  openDateTimeDialog(campaign: CampaignObject, eventObject: EventObject) {
    this.campaign = campaign

    this.dateTimeDialogRef = this.dialog.open(CampaignDateTimeFormComponent, {
      data: { campaign: campaign || new CampaignObject({}, this.campaignTypeProvider), eventObject: eventObject }
    });

    this.closeDateTimeDialog()
  }

  closeDateTimeDialog() {
    // dateTimeDialog - afterClosed()
    if (this.dateTimeDialogRef) {
      this.dateTimeDialogRef.afterClosed().subscribe(dateTimeData => {
        if (dateTimeData) {
          this.dateTimeDialogData = dateTimeData

          // cancel:
          if (dateTimeData.nav === 'cancel') {
            return dateTimeData
          }

          // back: campaignTypeDialog
          if (dateTimeData.nav === 'back') {
            this.openCampaignTypeDialog(this.campaignTypeDialogData.data.campaign, this.campaignTypeDialogData.data.eventObj)
          }

          // next: rateDialog
          if (dateTimeData.nav === 'next' && dateTimeData.data.campaign.campaignTypeId !== 4) {
            this.openRateDialog(dateTimeData.data.campaign, dateTimeData.data.eventObj)
          }

          // if (next && campaignType === Instagram): instagramPhotoDialog
          if (dateTimeData.nav === 'next' && dateTimeData.data.campaign.campaignTypeId === 4) {
            this.openSummaryDialog(dateTimeData.data.campaign, dateTimeData.data.eventObj)
          }
        }
      })
    }
  }

  openRateDialog(campaign: CampaignObject, eventObject: EventObject) {
    this.campaign = campaign

    this.rateDialogRef = this.dialog.open(CampaignRateFormComponent, {
      data: { campaign: campaign || new CampaignObject({}, this.campaignTypeProvider), eventObject: eventObject }
    });

    this.closeRateDialog()
  }

  closeRateDialog() {
    // rateDialog - afterClosed()
    if (this.rateDialogRef) {
      this.rateDialogRef.afterClosed().subscribe(rateData => {
        if (rateData) {
          this.rateDialogData = rateData

          // cancel:
          if (rateData.nav === 'cancel') {
            return rateData
          }

          // if (Social Media Route && !Instagram) back: dateTime
          if (rateData.nav === 'back' && rateData.data.campaign.campaignTypeId !== 7 && rateData.data.campaign.campaignTypeId !== 8 && rateData.data.campaign.campaignTypeId !== 4) {
            this.openDateTimeDialog(this.dateTimeDialogData.data.campaign, this.dateTimeDialogData.data.eventObj)
          }

          // if (Social Media Route && !Instagram) next: summary
          if (rateData.nav === 'next' && rateData.data.campaign.campaignTypeId !== 7 && rateData.data.campaign.campaignTypeId !== 8 && rateData.data.campaign.campaignTypeId !== 4) {
            this.openSummaryDialog(rateData.data.campaign, rateData.data.eventObj)
          }

          // if (Location Route) back: locationMap
          if (rateData.nav === 'back' && rateData.data.campaign.campaignTypeId === 7) {
            this.openLocationMapDialog(this.campaignTypeDialogData.data.campaign, this.campaignTypeDialogData.data.eventObj, true)
          }

          // if (Location Route) next: locationDateTime
          if (rateData.nav === 'next' && rateData.data.campaign.campaignTypeId === 7) {
            this.openLocationDateTimeDialog(rateData.data.campaign, rateData.data.eventObj)
          }

          // if (Poster Route) back: campaignType
          if (rateData.nav === 'back' && rateData.data.campaign.campaignTypeId === 8) {
            this.openCampaignTypeDialog(this.campaignTypeDialogData.data.campaign, this.campaignTypeDialogData.data.eventObj)
          }

          // if (Poster Route) next: posterDateTime
          if (rateData.nav === 'next' && rateData.data.campaign.campaignTypeId === 8) {
            this.openPosterDateTimeDialog(rateData.data.campaign, rateData.data.eventObj)
          }
        }
      })
    }
  }

  openPosterDateTimeDialog(campaign: CampaignObject, eventObject: EventObject) {
    this.campaign = campaign

    this.posterDateTimeDialogRef = this.dialog.open(PosterDateTimeFormComponent, {
      data: { campaign: campaign || new CampaignObject({}, this.campaignTypeProvider), eventObject: eventObject }
    });

    this.closePosterDateTimeDialog()
  }

  closePosterDateTimeDialog() {
    // posterDateTimeDialog - afterClosed()
    if (this.posterDateTimeDialogRef) {
      this.posterDateTimeDialogRef.afterClosed().subscribe(posterDateTimeData => {
        if (posterDateTimeData) {
          this.posterDateTimeDialogData = posterDateTimeData

          // cancel:
          if (posterDateTimeData.nav === 'cancel') {
            return posterDateTimeData
          }

          // back: rate
          if (posterDateTimeData.nav === 'back') {
            this.openRateDialog(this.rateDialogData.data.campaign, this.rateDialogData.data.eventObj)
          }

          // next: summary
          if (posterDateTimeData.nav === 'next') {
            this.openSummaryDialog(posterDateTimeData.data.campaign, posterDateTimeData.data.eventObj)
          }
        }
      })
    }
  }

  openLocationMapDialog(campaign: CampaignObject, eventObject: EventObject, back?: boolean ) {
    this.campaign = campaign

    this.locationMapDialogRef = this.dialog.open(LocationMapFormComponent, {
      data: { campaign: campaign || new CampaignObject({}, this.campaignTypeProvider), eventObject: eventObject, isBack: back }
    });

    this.closeLocationMapDialog()
  }

  closeLocationMapDialog() {
    // locationMapDialog - afterClosed()
    if (this.locationMapDialogRef) {
      this.locationMapDialogRef.afterClosed().subscribe(locationMapData => {
        if (locationMapData) {
          this.locationMapDialogData = locationMapData

          // cancel:
          if (locationMapData.nav === 'cancel') {
            return locationMapData
          }

          // back: campaignTypeDialog
          if (locationMapData.nav === 'back') {
            this.openCampaignTypeDialog(this.campaignTypeDialogData.data.campaign, this.campaignTypeDialogData.data.eventObj)
          }

          // next: rateDialog
          if (locationMapData.nav === 'next') {
            this.openRateDialog(locationMapData.data.campaign, locationMapData.data.eventObj)
          }
        }
      })
    }
  }

  openLocationDateTimeDialog(campaign: CampaignObject, eventObject: EventObject) {
    this.campaign = campaign

    this.locationDateTimeDialogRef = this.dialog.open(LocationDateTimeFormComponent, {
      data: { campaign: campaign || new CampaignObject({}, this.campaignTypeProvider), eventObject: eventObject }
    });

    this.closeLocationDateTimeDialog()
  }

  closeLocationDateTimeDialog() {
    // locationDateTimeDialog - afterClosed()
    if (this.locationDateTimeDialogRef) {
      this.locationDateTimeDialogRef.afterClosed().subscribe(locationDateTimeData => {
        if (locationDateTimeData) {
          this.locationDateTimeDialogData = locationDateTimeData

          // cancel:
          if (locationDateTimeData.nav === 'cancel') {
            return locationDateTimeData
          }

          // back: rate
          if (locationDateTimeData.nav === 'back') {
            this.openRateDialog(this.rateDialogData.data.campaign, this.rateDialogData.data.eventObj)
          }

          // next: summaryDialog
          if (locationDateTimeData.nav === 'next') {
            this.openSummaryDialog(locationDateTimeData.data.campaign, locationDateTimeData.data.eventObj)
          }
        }
      })
    }
  }

  openSummaryDialog(campaign: CampaignObject, eventObject: EventObject, isEdit?: boolean) {
    this.campaign = campaign
    this.isEdit = isEdit

    this.summaryDialogRef = this.dialog.open(CampaignDetailsDialog, {
      data: { campaign: campaign || new CampaignObject({}, this.campaignTypeProvider), eventObject: eventObject, isEdit: isEdit }
    });

    this.closeSummaryDialog()
  }

  closeSummaryDialog() {
    // summaryDialog - afterClosed()
    if (this.summaryDialogRef) {
      this.summaryDialogRef.afterClosed().subscribe(summaryData => {
        if (summaryData) {
          this.summaryDialogData = summaryData

          // cancel:
          if (summaryData.nav === 'cancel') {
            return summaryData
          }

          // if ('back' && campaignType === Social Media): Rate
          if (summaryData.nav === 'back' && summaryData.data.campaign.campaignTypeId !== 7 && summaryData.data.campaign.campaignTypeId !== 8 && summaryData.data.campaign.campaignTypeId !== 4) {
            this.openRateDialog(this.rateDialogData.data.campaign, this.rateDialogData.data.eventObj)
          }

          // if ('back' && campaignType === Instagram): InstagramPhoto
          if (summaryData.nav === 'back' && summaryData.data.campaign.campaignTypeId === 4) {
            this.openDateTimeDialog(this.instagramPhotoDialogData.data.campaign, this.instagramPhotoDialogData.data.eventObj)
          }

          // if ('back' && campaignType === Location): LocationDateTime
          if (summaryData.nav === 'back' && summaryData.data.campaign.campaignTypeId === 7) {
            this.openLocationDateTimeDialog(this.locationDateTimeDialogData.data.campaign, this.locationDateTimeDialogData.data.eventObj)
          }

          // if ('back' && campaignType === Poster): PosterDateTime
          if (summaryData.nav === 'back' && summaryData.data.campaign.campaignTypeId === 8) {
            this.openPosterDateTimeDialog(this.posterDateTimeDialogData.data.campaign, this.posterDateTimeDialogData.data.eventObj)
          }

          // next: saveCampaign
          if (summaryData.nav === 'next') {
            if (!!summaryData.data.campaign) {
              summaryData.data.campaign.applyFormFields(this.campaignDetailsMapProvider.map);

              if (this.campaignTypeProvider.isRouteCampaign(summaryData.data.campaign.campaignTypeId)) {
                this.saveCampaignRoute(summaryData.data.campaign.campaignRoute, summaryData.data.campaign.campaignTypeId).subscribe((putCampaignRouteResult) => {

                  let campaignRouteId = putCampaignRouteResult;
                  summaryData.data.campaign.campaignRouteId = campaignRouteId;

                  this.saveCampaign(summaryData.data.campaign);
                });
              }
              else {
                this.saveCampaign(summaryData.data.campaign);
              }
            }
          }
        }
      })
    }
  }

  saveCampaign(campaign: CampaignObject) {
    let campaignRoute = campaign.campaignRoute;
    campaign.campaignRoute = null; // TODO: Should not have to null this before being passed

    if (campaign.id != null) {
      this.campaignService.putCampaign(campaign).subscribe((putCampaignResult) => {
        this.campaignService.change.emit(campaign.id);
        this.showSnackbar("Campaign Updated");
      }, (errorResult) => {
        console.log(errorResult);
        this.showSnackbar("Failed to save Campaign");
      });
    }
    else {
      this.campaignService.postCampaign(campaign).subscribe((postCampaignResult) => {
        var results = postCampaignResult.json();

        this.campaignService.change.emit(results.id);

        if (results.campaignTypeId === 4) {
          var base64 = this.campaignService.base64;
          this.campaignService.uploadImage(results.id, base64).subscribe((uploadResult) => {
            console.log(uploadResult);
          })
        }

        this.showSnackbar("Campaign Created");
      }, (errorResult) => {
        console.log(errorResult);
        this.showSnackbar("Failed to create Campaign");
      });
    }
    campaign.campaignRoute = campaignRoute;
  }

  saveCampaignRoute(campaignRoute: CampaignRoute, campaignTypeId: number) {
    campaignRoute.map = null; // TODO: Should not have to null this before being passed

    //if (this.isEdit) {
    //  campaignRoute.campaignRouteMarkers = this.campaignDetailsMapProvider.getCampaignRouteMarkers()
    //}

    campaignRoute.campaignRouteMarkers = this.campaignDetailsMapProvider.getCampaignRouteMarkers()

    // TODO: applyFormFields
    campaignRoute.campaignRouteMarkers.forEach((campaignRouteMarker) => {
      switch (campaignRouteMarker.typeId) {
        case 1: // Poster-Markers
          let marker = <google.maps.Marker>campaignRouteMarker.marker;
          campaignRouteMarker.latitude = marker.getPosition().lat();
          campaignRouteMarker.longitude = marker.getPosition().lng();
          break;

        case 2: // Location-Circles
          let circleMarker = <google.maps.Circle>campaignRouteMarker.marker;
          campaignRouteMarker.latitude = circleMarker.getCenter().lat();
          campaignRouteMarker.longitude = circleMarker.getCenter().lng();
          campaignRouteMarker.radius = circleMarker.getRadius();
          break;

        case 3:
          // TODO: DON'T SAVE THESE
          break;
      }

      // TODO: Handle deleted markers (in the controller neighborhood)
      campaignRouteMarker.campaignRouteId = campaignRoute.id;
      campaignRouteMarker.marker = null;
    });

    campaignRoute.campaignTypeId = campaignTypeId;

    if ((campaignRoute.id != null) && (campaignRoute.id != "") && (campaignRoute.id != this.campaignTypeProvider.getDefaultRouteId(campaignRoute.campaignTypeId))) {
      return this.campaignService.putCampaignRoute(campaignRoute);
    }
    else {
      return this.campaignService.postCampaignRoute(campaignRoute);
    }
  }

  getCampaign() {
    return this.campaign;
  }

  showSnackbar(message: string) {
    // Get the snackbar DIV
    let x = document.getElementById("snackbar");

    // Add the "show" class to DIV
    x.className = "show";
    x.innerText = message;

    // After 3 seconds, remove the show class from DIV
    setTimeout(function () { x.className = x.className.replace("show", ""); }, 3000);
  }
}
