import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sort'
})

export class SortPipe implements PipeTransform {

  transform(value: any, orderedBy: any, startDate?: number, endDate?: number): any {
    let today = new Date().getTime()

    if (orderedBy === 'all') {
      value = value.filter((event) => true)
    }

    if (orderedBy === 'past') {
      value = value.filter((event) => new Date(event.startTime).getTime() < today)
    }

    if (orderedBy === 'upcoming') {
      value = value.filter((event) => new Date(event.startTime).getTime() > today)
    }

    if (orderedBy === 'custom') {
      value = value.filter((event) => new Date(event.startTime).setHours(0, 0, 0, 0) >= startDate && new Date(event.startTime).setHours(0, 0, 0, 0) <= endDate)
    }

    return value;
  }

}
