// External
import * as moment from 'moment';

// Internal
import { CampaignTypeProvider } from '../campaign-type.provider';
import { CampaignRoute } from './campaign-route.model';

export class CampaignObject {
  id: string;
  eventDetailsId: string;
  campaignTypeId: number;
  isPreferredPromoterOnly: boolean;
  campaignIndex: number;
  startDate: Date = null;
  endDate: Date = null;
  startTime: Date = null;
  endTime: Date = null;
  weekdays: string;
  publicComment: string;
  campaignRouteId: string;
  visibility: number;
  rate: number;
  quantitySold: number;
  promoterName: string; // TODO: Add to C# model
  duration: number;
  imageFileId: string;

  formFields: CampaignObjectFormFields; // Not include in C# DomainObject
  campaignRoute: CampaignRoute;

  // Providers
  campaignTypeProvider: CampaignTypeProvider;

  constructor(values: Object = {}, campaignTypeProvider: CampaignTypeProvider) {
    this.campaignTypeProvider = campaignTypeProvider;

    Object.assign(this, values);

    this.promoterName = "Daniel M"; // TODO: Remove this once added to C# model

    this.updateFormFields();
  }

  stdTimezoneOffset() {
    var jan = new Date(new Date().getFullYear(), 0, 1);
    var jul = new Date(new Date().getFullYear(), 6, 1);
    return Math.max(jan.getTimezoneOffset(), jul.getTimezoneOffset());
  }

  isDstObserved() {
    return new Date().getTimezoneOffset() < this.stdTimezoneOffset();
  }

  /*
   * Updates this.formFields (used by UI) based on this properties
   */
  updateFormFields() {
    this.formFields = new CampaignObjectFormFields();

    if (!!this.campaignTypeId) {
      this.formFields.campaignTypeId = this.campaignTypeId;
      this.formFields.isPreferredPromoterOnly = this.isPreferredPromoterOnly;
      this.formFields.campaignTypeName = this.campaignTypeProvider.getName(this.campaignTypeId);
      this.formFields.campaignTypeIcon = this.campaignTypeProvider.getIcon(this.campaignTypeId);
      this.formFields.rate = !!this.rate ? this.rate : this.campaignTypeProvider.getDefaultRate(this.campaignTypeId);
    }

    // Times
    this.startTime = (this.startTime === null) ? new Date(0, 0, 0, 0, 0, 0, 0) : new Date(this.startTime); // not applicable to Mobile; might still be for Web
    this.endTime = (this.endTime === null) ? new Date(0, 0, 0, 0, 0, 0, 0) : new Date(this.endTime); // not applicable to Mobile; might still be for Web
    this.formFields.startTime = this.startTime.toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true, timeZone: 'UTC' });
    this.formFields.endTime = this.endTime.toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true, timeZone: 'UTC' });

    this.updateTimePeriod();

    // Dates
    if (this.startDate !== null && this.startDate.toString() === "1970-01-01T00:00:00") this.startDate = null;
    if (this.endDate !== null && this.endDate.toString() === "1970-01-01T00:00:00") this.endDate = null;
    this.formFields.startDate = this.startDate;
    this.formFields.endDate = this.endDate;

    this.updateDateRange();

    this.formFields.duration = this.duration;

    // Weekdays
    if (this.weekdays == null) this.weekdays = "1111111";

    // CampaignRoute
    this.formFields.campaignRouteId = this.campaignRouteId;

    this.updateName();

    this.campaignRoute = new CampaignRoute();
  }

  /*
   * Updates this properties based on this.formFields (from the UI)
   */
  applyFormFields(map: google.maps.Map) {
    this.campaignTypeId = this.formFields.campaignTypeId;
    this.isPreferredPromoterOnly = this.formFields.isPreferredPromoterOnly;
    this.rate = this.formFields.rate;

    this.startTime = moment(this.formFields.startTime, 'LT').utc(true).toDate();
    this.endTime = moment(this.formFields.endTime, 'LT').utc(true).toDate();

    this.startDate = new Date(this.formFields.startDate);
    this.endDate = new Date(this.formFields.endDate);

    this.duration = this.formFields.duration;

    if (this.campaignTypeProvider.isRouteCampaign(this.campaignTypeId)) {
      this.campaignRouteId = this.formFields.campaignRouteId;
      this.campaignRoute.applyFormFields(map);
    }
    else {
      // TODO: this.campaignRouteId = "" or null ?
    }
  }

  updateTimePeriod() {
    if ((this.formFields.startTime === "12:00 AM") && (this.formFields.endTime === "12:00 AM")) this.formFields.timePeriod = "any";
    else if ((this.formFields.startTime === "6:00 AM") && (this.formFields.endTime === "12:00 PM")) this.formFields.timePeriod = "morning";
    else if ((this.formFields.startTime === "12:00 PM") && (this.formFields.endTime === "5:00 PM")) this.formFields.timePeriod = "afternoon";
    else if ((this.formFields.startTime === "5:00 PM") && (this.formFields.endTime === "9:00 PM")) this.formFields.timePeriod = "evening";
    else if ((this.formFields.startTime === "9:00 PM") && (this.formFields.endTime === "6:00 AM")) this.formFields.timePeriod = "night";
    else this.formFields.timePeriod = "custom";

    this.updateName();
  }

  applyTimePeriod(timePeriod) {
    switch (timePeriod) {
      case "any":  // Any Time
        this.formFields.startTime = "12:00 AM";
        this.formFields.endTime = "12:00 AM";
        break;

      case "morning": // Morning (6am-noon)
        this.formFields.startTime = "6:00 AM";
        this.formFields.endTime = "12:00 PM";
        break;

      case "afternoon": // Afternoon (noon-5pm)
        this.formFields.startTime = "12:00 PM";
        this.formFields.endTime = "5:00 PM";
        break;

      case "evening": // Evening (5pm-9pm)
        this.formFields.startTime = "5:00 PM";
        this.formFields.endTime = "9:00 PM";
        break;

      case "night": // Night (9pm-6am)
        this.formFields.startTime = "9:00 PM";
        this.formFields.endTime = "6:00 AM";
        break;

      case "custom": // Custom Time Period
        break;
    }
  }

  updateDateRange() {
    if ((this.formFields.startDate === null) && (this.formFields.endDate === null)) {
      this.formFields.dateRange = "any";
    }
    else {
      this.formFields.dateRange = "custom";
    }

    this.updateName();
  }

  applyDateRange(dateRange) {
    switch (dateRange) {
      case "any":
        this.formFields.startDate = null;
        this.formFields.endDate = null;
        break;

      case "custom":
        break;
    }
  }

  timePeriodFormatted() {
    switch (this.formFields.timePeriod) {
      case "any": return "Any Time";
      case "morning": return "Morning (6am-noon)";
      case "afternoon": return "Afternoon (noon-5pm)";
      case "evening": return "Evening (5pm-9pm)";
      case "night": return "Night (9pm-6am)";
      case "custom": return this.formFields.startTime + " to " + this.formFields.endTime;
    }

    return "Invalid Times";
  }

  dateRangeFormatted() {
    switch (this.formFields.dateRange) {
      case "any": return "Any Date";
      case "custom":
        return (new Date(this.formFields.startDate)).toLocaleDateString() + " to " + (new Date(this.formFields.endDate)).toLocaleDateString();
    }

    return "Invalid Dates";
  }

  dateFormatted() {
    switch (this.formFields.dateRange) {
      case "any": return "Any Date";
      case "custom":
        return (new Date(this.formFields.startDate)).toLocaleDateString();
    }

    return "Invalid Dates";
  }

  weekdaysFormatted() {
    switch (this.weekdays) {
      case "1111111": return "Any Day";
      case "0111110": return "Weekdays";
      case "1000001": return "Weekends";
      case "0100000": return "Monday";
      case "0010000": return "Tuesday";
      case "0001000": return "Wednesday";
      case "0000100": return "Thursday";
      case "0000010": return "Friday";
      case "0000001": return "Saturday";
      case "1000000": return "Sunday";
      //case "0000000": return "Custom Days";
    }

    return "Invalid Days";
  }

  updateName() {
    var name = this.campaignTypeProvider.getName(this.campaignTypeId);

    switch (this.campaignTypeId) {
      case 1: // Link
        name = "Link to Sell Tickets"
        break;
      case 2: // Facebook
      case 3: // Twitter
      case 4: // Instagram
        var timePeriod = this.timePeriodFormatted(); // Any Time
        var dateRange = this.dateRangeFormatted(); // Any Date
        var weekdays = this.weekdaysFormatted(); // Any Day

        if (timePeriod != "Any Time") {
          name += ": " + timePeriod;
        }
        else if (dateRange != "Any Date") {
          name += ": " + dateRange;
        }
        else if (weekdays != "Any Day") {
          name += ": " + weekdays;
        }
        break;
      case 5: // PromoterQR
        name = "QR Code";
        break;
    }

    this.formFields.campaignName = name;
  }

  private getMinutes(date: Date, utc: boolean): number {
    return utc
      ? date.getUTCHours() * 60 + date.getUTCMinutes()
      : date.getHours() * 60 + date.getMinutes();
  }

  private addDays(date: Date, days: number) {
    var updatedDate = new Date(date);
    updatedDate.setDate(updatedDate.getDate() + days);
    return updatedDate;
  }

  private isWithinTime(): boolean {
    var startTimeMinutes = this.getMinutes(this.startTime, true);
    var endTimeMinutes = this.getMinutes(this.endTime, true);

    // If startTime and endTime are not specified (or the same), return true
    if (startTimeMinutes == endTimeMinutes) return true;

    var currentMinutes = this.getMinutes(new Date(), false);

    // If currentMinutes is between startTime and endTime, return true
    if ((startTimeMinutes < endTimeMinutes) // handle startTime < endTime
      && (startTimeMinutes <= currentMinutes) && (currentMinutes < endTimeMinutes)) return true;

    if ((startTimeMinutes > endTimeMinutes) // handle startTime > endTime (crosses midnight)
      && (startTimeMinutes <= currentMinutes) || (currentMinutes < endTimeMinutes)) return true;

    // Else return false
    return false;
  }

  private isWithinDate(): boolean {
    var currentDate = new Date();

    // If startDate is specified and is currentDate < startDate, then return false
    if ((this.startDate != null) && (currentDate < new Date(this.startDate))) return false;

    // If endDate is specified and is currentDate > endDate + 1 day (endDate being inclusive), then return false
    if ((this.endDate != null) && (currentDate > this.addDays(this.endDate, 1))) return false;

    // Else return false
    return true;
  }

  private isWithinWeekday(): boolean {
    // If weekdays is not specified, then return true
    if (this.weekdays == null) return true;

    // If weekdays[currentDay] = "1", then return true, else false
    var currentDay = (new Date()).getDay();
    return (this.weekdays.substr(currentDay, 1) == "1");
  }

  canPostNow() {
    var canPost = this.isWithinTime() && this.isWithinDate() && this.isWithinWeekday();

    return canPost;
  }
}

export class CampaignObjectFormFields {
  campaignTypeId: number;
  isPreferredPromoterOnly: boolean;
  campaignTypeName: string;
  campaignTypeIcon: string;
  campaignName: string;

  timePeriod: string;
  dateRange: string;

  startDate: Date;
  endDate: Date;
  startTime: string;
  endTime: string;

  campaignRouteId: string;
  routeName: string;
  duration: number;

  rate: number;
}
