// External
import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject, Subject, interval, pipe } from 'rxjs';
import { switchMap, map } from 'rxjs/operators';

// Internal
import { EventObject } from './event.models/event-object.model';
import { EventService } from './event.service';
import { CampaignService } from '../campaign/campaign.service';

@Injectable()
export class EventProvider {
  selectedEvent = new Subject();
  selectedEventObject: EventObject = null;

  sortSubject = new Subject<any>();
  searchSubject = new Subject<{ searchString: string, showListView: string }>();
  customDateSubject = new Subject<any>();

  constructor(private eventService: EventService, private campaignService: CampaignService) {
  }

  filterEvents(orderedBy) {
    this.sortSubject.next(orderedBy)
  }

  searchEvents(query: { searchString: string, showListView: string }) {
    this.searchSubject.next(query)
  }

  searchPromoters(query: { searchString: string, showListView: string }) {
      this.searchSubject.next(query)
  }

  customDateRange(dateRange: { orderedBy: string, startDate: number, endDate: number }) {
    this.customDateSubject.next(dateRange)
  }

  selectEvent(eventDetails: EventObject) {    
    if (!!this.selectedEventObject) this.selectedEventObject.selected = false;
    this.selectedEvent.next(eventDetails);
    this.selectedEventObject = eventDetails;
    if (!!this.selectedEventObject) this.selectedEventObject.selected = true;
  }

  enableEvent(eventId: string, defaultRate: number) {
    this.eventService.enableEvent(eventId, defaultRate).subscribe((enableEventResult) => {
      this.selectedEventObject.eventStatus = 1; // TODO: Use enum, determine if this is an acceptable place to set this

      this.campaignService.change.emit(null);
      //this.showSnackbar("Campaign Created");
    }, (errorResult) => {
      console.log(errorResult);
      //this.showSnackbar("Failed to create Campaign");
    });
  }

  updateSales(eventId: string) {
    var observable = this.eventService.updateSales(eventId);
    /*
    observable.subscribe((refreshSalesResult) => {
      console.log("Sales refreshed");
      //this.showSnackbar("Sales Refreshed");

      //this.selectEvent(this.selectedEventObject);
    }, (errorResult) => {
      console.log(errorResult);
      //this.showSnackbar("Failed to create Campaign");
    });
    */
    return observable;
  }
}
