import { Component, EventEmitter, Output } from '@angular/core';
import { faSlidersH, faSearch, faTimes } from '@fortawesome/free-solid-svg-icons';
import { MatDatepickerInputEvent } from '@angular/material';
import * as moment from 'moment'
import { EventProvider } from '../event/event.provider';

@Component({
  selector: 'event-list-filter',
  templateUrl: './event-list-filter.component.html',
  styleUrls: ['./event-list-filter.component.css']
})

export class EventListFilterComponent {
  //FONT-AWESOME-ICONS
  faSlidersH = faSlidersH
  faSearch = faSearch
  faTimes = faTimes

  //SEARCH
  search: any;


  //UI
  openFilter: boolean = true
  openDateModal: boolean = false

  //ACTIVE SORT-BTNS
  pastBtnActive: boolean = false;
  allBtnActive: boolean = false;
  upcomingBtnActive: boolean = false;
  customBtnActive: boolean = false;

  //DATEPICKER
  startingDate
  endingDate
  customDateRange: string;

  //DATEPICKER VALIDATION
  startDateIsInvalid: boolean = true;
  startDateIsTouched: boolean = false;
  endDateIsInvalid: boolean = true;
  endDateIsTouched: boolean = false;
  customDatesAreInvalid: boolean = true;
  @Output() dateChange: EventEmitter<MatDatepickerInputEvent<any>>


  constructor(private eventProvider: EventProvider) { }

  ngOnInit() {
    this.showEvents('upcoming')
  }

  clearSearch() {
    this.search = '';
    this.searchEvents('', 'list');
  }

  onStartDateBlur() {
    this.startDateIsTouched = true
  }

  onEndDateBlur() {
    this.endDateIsTouched = true
  }

  validateStartDate() {
    let startDate = moment(this.startingDate, 'M/D/YYYY', true).isValid()
    startDate ? this.startDateIsInvalid = false : this.startDateIsInvalid = true
  }

  validateEndDate() {
    let endDate = moment(this.endingDate, 'M/D/YYYY', true).isValid()
    endDate ? this.endDateIsInvalid = false : this.endDateIsInvalid = true
  }

  onValidateSaveDatePicker() {
    this.validateStartDate();
    this.validateEndDate();
    !this.startDateIsInvalid && !this.endDateIsInvalid ? this.customDatesAreInvalid = false : this.customDatesAreInvalid = true
  }

  onSaveDatePicker() {
    let startDate = new Date(this.startingDate).getTime()
    let endDate = new Date(this.endingDate).getTime()

    this.customDateRange = `${moment(startDate).format('MMM DD')} - ${moment(endDate).format('MMM DD')}`

    this.eventProvider.customDateRange({
      orderedBy: 'custom',
      startDate,
      endDate
    })

    this.openDateModal = false
  }

  onCancelDatePicker() {
    this.openDateModal = false;
  }

  openDatePicker() {
    this.customBtnActive = true;
    this.allBtnActive = false;
    this.pastBtnActive = false;
    this.upcomingBtnActive = false;

    this.openDateModal = !this.openDateModal
  }

  showEvents(orderedBy) {
    if (orderedBy === 'all') {
      this.allBtnActive = true;
      this.pastBtnActive = false;
      this.upcomingBtnActive = false;
      this.customBtnActive = false
      this.openDateModal = false
    }

    if (orderedBy === 'past') {
      this.pastBtnActive = true;
      this.allBtnActive = false;
      this.upcomingBtnActive = false;
      this.customBtnActive = false
      this.openDateModal = false
    }

    if (orderedBy === 'upcoming') {
      this.upcomingBtnActive = true;
      this.allBtnActive = false;
      this.pastBtnActive = false;
      this.customBtnActive = false
      this.openDateModal = false
    }

    this.customDateRange = undefined

    this.eventProvider.filterEvents(orderedBy)
  }

  searchEvents(searchString, showListView) {
    this.openFilter = true;
    this.eventProvider.searchEvents({
      searchString,
      showListView
    })
  }

  toggleFilterModal() {
    this.openFilter = !this.openFilter
  }
}
