// External
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

// Internal
import { CampaignRouteMarker } from '../campaign.models/campaign-route-marker.model';

@Injectable()
export class CampaignDetailsMapProvider {
  campaignRouteMarkersPublisher = new Subject();
  labels = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
  labelIndex = 0;

  locationSelectedOptions = {
    opacity: 1.0
  }

  locationDeselectedOptions = {
    opacity: 0.6
  }

  circleSelectedOptions: google.maps.CircleOptions = {
    strokeColor: '#00FF00',
    strokeOpacity: 0.5,
    strokeWeight: 1,
    fillColor: '#00FF00',
    fillOpacity: 0.3,
  };

  circleDeselectedOptions: google.maps.CircleOptions = {
    strokeColor: '#0000FF',
    strokeOpacity: 0.5,
    strokeWeight: 1,
    fillColor: '#0000FF',
    fillOpacity: 0.3,
  }

  public circleRadiusVenue: number = 300;
  public circleRadiusAdd: number = 300;
  public circleRadiusSearchResult: number = 300;
  public locationCircle
  public prevLocation: Number = null
  public campaignRouteMarkers: CampaignRouteMarker[] = [];
  public selectedMarker: CampaignRouteMarker;
  public map: google.maps.Map;

  constructor() {
  }

  getLocationCircle() {
    return this.locationCircle
  }

  setLocationCircle(locationCircle) {
    this.locationCircle = locationCircle
  }

  resetPrevLocation() {
    this.prevLocation = null
  }

  deleteSelectedMarker() {
    if (this.selectedMarker != null) {
      let campaignRouteMarkerIndex = this.campaignRouteMarkers.findIndex((campaignRouteMarker) => { return campaignRouteMarker.label == this.selectedMarker.label });
      if (campaignRouteMarkerIndex >= 0) this.campaignRouteMarkers.splice(campaignRouteMarkerIndex, 1);

      this.selectedMarker.marker.setMap(null);
    }

    this.prevLocation = null
  }

  searchPlaces(map: google.maps.Map, criteria: string, location: google.maps.LatLng, campaignTypeId: number) {
    let placesService = new google.maps.places.PlacesService(map);

    let request = {
      keyword: criteria,
      location: {
        lat: location.lat(),
        lng: location.lng()
      },
      //radius: 50000, // ff (STRYD-219) : there's a conflict here but might not needed
      rankBy: google.maps.places.RankBy.DISTANCE // ff (STRYD-219) : rankBy does not accept string and does not work with radius
    };

    placesService.nearbySearch(request, this.nearbySearchCallback(map, this, campaignTypeId, request).bind(this)); // ff (STRYD-219) : binding this since this was passsed
  }

  private nearbySearchCallback(map: google.maps.Map, campaignDetailsMapProvider: CampaignDetailsMapProvider, campaignTypeId: number, request: any) {

    return function (results, status) {
      if (status === 'ZERO_RESULTS') {
        // if nearbySearch() returns ZERO_RESULTS, use keyword to make a TextSearch
        let placesService = new google.maps.places.PlacesService(map);

        let textSearchRequest = {
          location: request.location,
          radius: 50000,
          query: request.keyword
        }

        placesService.textSearch(textSearchRequest, this.textSearchCallback(map, this, campaignTypeId, request).bind(this))
      }

      if (status == google.maps.places.PlacesServiceStatus.OK) {
        let center = new google.maps.LatLng(results[0].geometry.location.lat(), results[0].geometry.location.lng());
        this.setLocationCircle({ lat: results[0].geometry.location.lat(), lng: results[0].geometry.location.lng() })
        map.setCenter(center);

        let currentLocation = +results[0].geometry.location.lat() + +results[0].geometry.location.lng()

        if (this.prevLocation !== currentLocation) {
          switch (campaignTypeId) {
            case 7: // Location
              campaignDetailsMapProvider.clearCampaignRouteMarkers(); // Delete existing circle
              campaignDetailsMapProvider.addCircleMarker(map, center, campaignDetailsMapProvider.circleRadiusSearchResult);
              break;

            case 8: // Poster
              campaignDetailsMapProvider.addLocationMarker(map, center, campaignDetailsMapProvider.nextLabel(), "", null);
              break;
          }

          this.prevLocation = currentLocation
        }
      }
    }
  }

  textSearchCallback(map: google.maps.Map, campaignDetailsMapProvider: CampaignDetailsMapProvider, campaignTypeId: number, request: any) {
    return function (results, status) {
      if (status == google.maps.places.PlacesServiceStatus.OK) {
        let center = new google.maps.LatLng(results[0].geometry.location.lat(), results[0].geometry.location.lng());
        this.setLocationCircle({ lat: results[0].geometry.location.lat(), lng: results[0].geometry.location.lng() })

        map.setCenter(center);

        switch (campaignTypeId) {
          case 7: // Location
            campaignDetailsMapProvider.clearCampaignRouteMarkers(); // Delete existing circle
            campaignDetailsMapProvider.addCircleMarker(map, center, campaignDetailsMapProvider.circleRadiusSearchResult);
            break;

          case 8: // Poster
            campaignDetailsMapProvider.addLocationMarker(map, center, campaignDetailsMapProvider.nextLabel(), "", null);
            break;
        }
      }
    }
  }

  clearCampaignRouteMarkers() {
    this.campaignRouteMarkers.forEach((campaignRouteMarker) => {
      if (!!campaignRouteMarker.marker) {
        campaignRouteMarker.marker.setMap(null);
        //campaignRouteMarker.marker.setMap(this.map);
      }
      else {
        // TODO: Understand how it gets here, it does
      }
    });

    this.campaignRouteMarkers = [];

    this.campaignRouteMarkersPublisher.next(this.campaignRouteMarkers);

    this.resetLabelIndex();
  }

  getCampaignRouteMarkers(): CampaignRouteMarker[] {
    return this.campaignRouteMarkers;
  }

  private selectMarker(campaignRouteMarker: CampaignRouteMarker) {
    this.deselectMarkers();

    this.selectedMarker = campaignRouteMarker;

    switch (campaignRouteMarker.typeId) {
      case 1: // Location
        (<google.maps.Marker>this.selectedMarker.marker).setOpacity(this.locationSelectedOptions.opacity);
        break;

      case 2: // Circle
        (<google.maps.Circle>this.selectedMarker.marker).setOptions(this.circleSelectedOptions);
        break;

      case 3: // Posted
        break;
    }
  }

  private deselectMarkers() {
    this.selectedMarker = null;

    this.campaignRouteMarkers.forEach((campaignRouteMarker) => {
      switch (campaignRouteMarker.typeId) {
        case 1: // Location
          (<google.maps.Marker>campaignRouteMarker.marker).setOpacity(this.locationDeselectedOptions.opacity);
          break;

        case 2: // Circle
          (<google.maps.Circle>campaignRouteMarker.marker).setOptions(this.circleDeselectedOptions);
          break;

        case 3: // Posted
          break;
      }
    });
  }

  // TODO: Make private now
  resetLabelIndex() {
    this.labelIndex = 0;
  }

  nextLabel() {
    return this.labels[this.labelIndex++ % this.labels.length];
  }

  addMarker(map: google.maps.Map, campaignRouteMarker: CampaignRouteMarker): CampaignRouteMarker {
    let center: google.maps.LatLng;

    switch (campaignRouteMarker.typeId) {
      case 1: // Location
        center = new google.maps.LatLng(campaignRouteMarker.latitude, campaignRouteMarker.longitude);
        return this.addLocationMarker(map, center, campaignRouteMarker.label, campaignRouteMarker.comment, campaignRouteMarker.photoFileId);

      case 2: // Circle
        center = new google.maps.LatLng(campaignRouteMarker.latitude, campaignRouteMarker.longitude);
        return this.addCircleMarker(map, center, campaignRouteMarker.radius);
    }
  }

  // Event Handlers - Map (private)
  addLocationMarker(map: google.maps.Map, center: google.maps.LatLng, label: string, comment: string, photoFileId: string): CampaignRouteMarker {
    let campaignRouteMarker = this.createLocationMarker(map, center, label, comment);

    campaignRouteMarker.photoFileId = photoFileId;

    if (campaignRouteMarker.photoFileId != null) {
      campaignRouteMarker.photoUrl = "../api/File/" + campaignRouteMarker.photoFileId;
      campaignRouteMarker.photoThumbnailUrl = "../api/File/" + campaignRouteMarker.photoFileId;
    }

    this.campaignRouteMarkers.push(campaignRouteMarker);
    this.campaignRouteMarkersPublisher.next(this.campaignRouteMarkers);
    this.selectMarker(campaignRouteMarker);

    return campaignRouteMarker;
  }

  private createLocationMarker(map: google.maps.Map, center: google.maps.LatLng, label: string, comment: string): CampaignRouteMarker {
    let locationDeselectedOptions = {
      ...this.locationDeselectedOptions,

      map: map,
      position: center,
      draggable: true,
      label: label,
      title: ""
    };

    let marker = new google.maps.Marker(locationDeselectedOptions);

    let campaignRouteMarker = new CampaignRouteMarker({ typeId: 1, marker: marker, label: label, comment: comment });// TODO: Use const for typeId 1?

    this.addLocationMarkerListeners(campaignRouteMarker);

    return campaignRouteMarker;
  }

  addCircleMarker(map: google.maps.Map, center: google.maps.LatLng, radius: number): CampaignRouteMarker {
    let campaignRouteMarker = this.createCircleMarker(map, center, radius);
    this.campaignRouteMarkers.push(campaignRouteMarker);
    this.campaignRouteMarkersPublisher.next(this.campaignRouteMarkers);
    this.selectMarker(campaignRouteMarker);

    return campaignRouteMarker;
  }

  createCircleMarker(map: google.maps.Map, center: google.maps.LatLng, radius: number): CampaignRouteMarker {
    let circleDeselectedOptions: any = Object.assign({
      map: map,
      center: center,
      draggable: true,
      editable: true,
      radius: radius
    }, this.circleDeselectedOptions);

    let marker = new google.maps.Circle(circleDeselectedOptions);

    let campaignRouteMarker = new CampaignRouteMarker({ typeId: 2, marker: marker });// TODO: Use const for typeId 2?

    this.addCircleMarkerListeners(campaignRouteMarker);

    return campaignRouteMarker;
  }

  private addCircleMarkerListeners(campaignRouteMarker: CampaignRouteMarker) {
    let marker = <google.maps.Circle>campaignRouteMarker.marker;

    marker.addListener('cursor_changed', (evt) => {
      this.selectMarker(campaignRouteMarker);
    });

    marker.addListener('mouseover', (evt) => {
      this.selectMarker(campaignRouteMarker);
    });

    marker.addListener('click', (evt) => {
      this.selectMarker(campaignRouteMarker);
    });

    marker.addListener('center_changed', (evt) => {
      let lat = campaignRouteMarker.marker.getCenter().lat()
      let lng = campaignRouteMarker.marker.getCenter().lng()

      this.setLocationCircle({lat, lng})
      this.selectMarker(campaignRouteMarker);
    });
  }

  private addLocationMarkerListeners(campaignRouteMarker: CampaignRouteMarker) {
    let marker = <google.maps.Marker>campaignRouteMarker.marker;

    marker.addListener('cursor_changed', (evt) => {
      this.selectMarker(campaignRouteMarker);
    });

    marker.addListener('mouseover', (evt) => {
      this.selectMarker(campaignRouteMarker);
    });

    marker.addListener('click', (evt) => {
      this.selectMarker(campaignRouteMarker);
    });
  }
}
