// External
import { Component } from '@angular/core';
import { of } from 'rxjs';

// Internal
import { CampaignObject } from '../campaign.models/campaign-object.model';
import { EventObject } from '../../event/event.models/event-object.model';
import { Promoter } from '../../promoter/promoter.models/promoter.model';
import { CampaignService } from '../campaign.service';
import { CampaignProvider } from '../campaign.provider';
import { EventProvider } from '../../event/event.provider';
import { CampaignTypeProvider } from '../campaign-type.provider';
import { switchMap } from 'rxjs/operators';

@Component({
  selector: 'campaign-list',
  templateUrl: './campaign-list.component.html',
  styleUrls: ['./campaign-list.component.css']
})

export class CampaignList {

  campaigns: any[]; // campaign object
  promoters: Promoter[];
  posterRoutePhotoStatus: any;

  public selectedEvent: EventObject;

  toggleView: string = 'campaigns';

  constructor(private campaignService: CampaignService, private campaignProvider: CampaignProvider, private campaignTypeProvider: CampaignTypeProvider, private eventProvider: EventProvider) {
    this.campaigns = [];

    var self = this;
    this.selectEvent(eventProvider.selectedEventObject);

    var subscription = eventProvider.selectedEvent.subscribe(
      function (x: EventObject) {
        self.selectEvent(x);
      },
      function (err) {
        console.log('Error: ' + err);
      },
      function () {
        console.log('Completed');
      }
    );
  }

  changeView(view: string) {
    this.toggleView = view
  }

  getCampaigns(eventDetailsId: string) {

    // this.campaigns = [];

    return this.campaignService.getCampaigns(eventDetailsId).pipe(
      switchMap((res) => {
        let sortedCampaigns = res.sort((n1, n2) => {
          if (this.campaignTypeProvider.getOrder(n1.campaignTypeId) > this.campaignTypeProvider.getOrder(n2.campaignTypeId)) {
            return 1;
          }
          if (this.campaignTypeProvider.getOrder(n1.campaignTypeId) < this.campaignTypeProvider.getOrder(n2.campaignTypeId)) {
            return -1;
          }
          return 0;
        });
        return of(sortedCampaigns);
      }),
      switchMap((campaigns) => {
        let pCount = 0;
        this.campaignService.getCampaignRoutes(campaigns[0]['campaignRouteId'])
          .subscribe((campaignRoutes) => {
            campaigns.forEach((campaign) => {
              if (campaign['campaignRouteId'] !== null) {
                for (let route of campaignRoutes) {
                  if (route['id'] === campaign['campaignRouteId']) {
                    campaign['campaignRoute'] = route;
                  }
                }
                if (campaign['formFields']['campaignTypeName'] === 'Poster') {
                  if (campaign['campaignRoute']['campaignRouteMarkers'].length > 0) {
                    for (let marker of campaign['campaignRoute']['campaignRouteMarkers']) {
                      if (marker['photoUrl']) {
                        pCount += 1;
                      }
                    }
                  }
                  this.posterRoutePhotoStatus[campaign['campaignRoute']['id']] = {
                    photoTotal: campaign['campaignRoute']['campaignRouteMarkers'].length,
                    photoCount: pCount
                  }
                }
              }
            });
          });
          return of(campaigns);
      })
    )
     
  }

  selectEvent(eventObject) {
    this.selectedEvent = eventObject;

    if (this.selectedEvent != null) {

      this.campaigns = [];
      this.posterRoutePhotoStatus = {};

      // this.getCampaigns(this.selectedEvent.id);
      this.getCampaigns(this.selectedEvent.id)
        .subscribe((result) => {
          this.campaigns = result;
        });

    }
    
  }

  editCampaign(campaign) {
    this.campaignProvider.editDialog(campaign, this.selectedEvent);
  }

  addCampaign() {
    
    let campaignObject = new CampaignObject({
      id: null,
      eventDetailsId: this.selectedEvent.id,
      campaignTypeId: 2, // TODO: Use enum
      campaignIndex: 0,
      startTime: Date.UTC(0, 0, 0, 0, 0, 0, 0),
      endTime: Date.UTC(0, 0, 0, 0, 0, 0, 0),
      visibility: 1 // TODO: Define enum
    }, this.campaignTypeProvider);
    
    var dialogRef = this.campaignProvider.openCampaignTypeDialog(campaignObject, this.selectedEvent);
    
  }

  getCampaignStatus(campaign) {
    let { handbillStatus, posterRouteStatus } = campaign;
    if (handbillStatus) {
      switch (handbillStatus.handbillStatusId) {
        case 1 :
          return 'claimed'
        case 2 :
          return 'started'
        case 3 : 
          return 'completed'
        case 4 :
          return 'accepted'
        case 5 :
          return 'rejected'
        default :
          return 'default'
      }
    }
    if (posterRouteStatus) {
      switch (posterRouteStatus.posterRouteStatusId) {
        case 1 :
          if (this.posterRoutePhotoStatus[campaign['campaignRoute']['id']]) {
            let count = this.posterRoutePhotoStatus[campaign['campaignRoute']['id']]['photoCount'];
            if (posterRouteStatus.posterRouteStatusId === 1 && count <= 0) {
              return 'claimed'
            }
            if (posterRouteStatus.posterRouteStatusId === 1 && count > 0) {
              return 'started'
            }
          }
          else {
            return 'default'
          }
        case 3 :
          return 'completed'
        case 4 :
          return 'accepted'
        case 5 :
          return 'rejected'
        default :
          return 'default'
      }
    }
  }

  ngOnInit() {
    this.campaignService.change.subscribe(campaignId => {
      // TODO: Consider upserting just the specified campaignId
      this.getCampaigns(this.selectedEvent.id);

      this.campaignService.getCampaigns(this.selectedEvent.id).subscribe((res) => {

        var sortedCampaigns = res.sort((n1, n2) => {
          if (this.campaignTypeProvider.getOrder(n1.campaignTypeId) > this.campaignTypeProvider.getOrder(n2.campaignTypeId)) {
            return 1;
          }

          if (this.campaignTypeProvider.getOrder(n1.campaignTypeId) < this.campaignTypeProvider.getOrder(n2.campaignTypeId)) {
            return -1;
          }

          return 0;
        });

        this.campaigns = sortedCampaigns;
      })
    });



  }
}
