// External
import { Injectable, Output, EventEmitter } from '@angular/core';
import { Http, Response } from '@angular/http';
import { map } from 'rxjs/operators';

// Internal
import { CampaignObject } from './campaign.models/campaign-object.model';
import { CampaignTypeProvider } from './campaign-type.provider';
import { CampaignRoute } from './campaign.models/campaign-route.model';

@Injectable()
export class CampaignService {
  private baseUrl: string = "..";
  base64: string;

  @Output() change: EventEmitter<string> = new EventEmitter();

  constructor(private http: Http, private campaignTypeProvider: CampaignTypeProvider) {

  }

  getCampaignTypeName(campaignTypeId: string) {

  }

  getCampaigns(eventDetailsId: string) {
    let apiUrl = this.baseUrl + '/api/Campaigns?eventDetailsId=' + eventDetailsId;

    return this.http.get(apiUrl).pipe(
      map((res: Response) => {
        return res.json().map((campaign) => new CampaignObject(campaign, this.campaignTypeProvider));
      })
    );
  }

  postCampaign(campaign: CampaignObject) {
    let apiUrl = this.baseUrl + '/api/Campaigns';

    return this.http.post(apiUrl, campaign).pipe(
      map((res: Response) => {
        return res;
      })
    );
  }

  putCampaign(campaign: CampaignObject) {
    let apiUrl = this.baseUrl + '/api/Campaigns';

    return this.http.put(apiUrl, campaign).pipe(
      map((res: Response) => {
        return res;
      })
    );
  }

  getCampaignRoutes(campaignRouteId) {
    let apiUrl = this.baseUrl + '/api/CampaignRoute';
    if (campaignRouteId !== null) apiUrl += "?campaignRouteId=" + campaignRouteId;
    
    return this.http.get(apiUrl).pipe(
      map((res: Response) => {
        return res.json().map((campaignRoute) => new CampaignRoute(campaignRoute));
      })
    );
  }

  postCampaignRoute(campaignRoute: CampaignRoute) {
    let apiUrl = this.baseUrl + '/api/CampaignRoute';

    return this.http.post(apiUrl, campaignRoute).pipe(
      map((res: Response) => {
        return res.text();
      })
    );
  }

  putCampaignRoute(campaignRoute: CampaignRoute) {
    let apiUrl = this.baseUrl + '/api/CampaignRoute';

    return this.http.put(apiUrl, campaignRoute).pipe(
      map((res: Response) => {
        return res.text();
      })
    );
  }

  deleteCampaign(id) {
    let apiUrl = this.baseUrl + '/api/Campaigns?id=' + id;

    return this.http.delete(apiUrl).pipe(
      map((res: Response) => {
        return res;
      })
    );
  }

  uploadImage(campaignId: string, image: string) {
    let apiUrl = this.baseUrl + '/api/Campaigns/UploadImage';

    let uploadImageRequest = {
      campaignId: campaignId,
      image: image
    };

    return this.http.post(apiUrl, uploadImageRequest).pipe(
      map((res: Response) => {
        return res.text();
      })
    );
  }

  passBase64(image: string) {
    this.base64 = image;
  }

  campaignPosterRouteStatusComplete(campaignId: string, options) {
    let apiUrl = this.baseUrl + '/api/CampaignRoute/UpdatePosterRouteStatus?campaignId=' + campaignId + '&status=' + options.status;
    return this.http.post(apiUrl, {}).pipe(
      map((res: Response) => {
        return res.text();
      })
    );
  }

  campaignHandbillRouteStatusComplete(campaignId: string, options) {
    let { status, duration } = options;
    let apiUrl = this.baseUrl + '/api/CampaignRoute/UpdateHandbillStatus?campaignId=' + campaignId + '&status=' + status + '&duration=' + duration;
    return this.http.post(apiUrl, {}).pipe(
      map((res: Response) => {
        return res.text();
      })
    );
  }

}
