// External
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable, pipe } from 'rxjs';
import { switchMap, map } from 'rxjs/operators';

@Injectable()
export class YourPromotersService {
    private baseUrl: string = "..";

    constructor(private http: Http) {

    }

    getPreferredPromoters() {

    }

    searchPreferredPromoter(searchString: string) {
        let apiUrl = this.baseUrl + '/api/Venues/Promoters/' + searchString;

        return this.http.get(apiUrl).pipe(
            map((res: Response) => {
                return res;
            })
        );
    }

    addPreferredPromoter(promoterId: string) {
        let apiUrl = this.baseUrl + '/api/Venues/Promoters/';

        console.log('service hit');

        let preferredPromoterRequest = {
            promoterId: promoterId
        };

        return this.http.post(apiUrl, preferredPromoterRequest).pipe(
            map((res: Response) => {
                console.log('Preferred Promoter Res: ' + res);
                return res;
            })
        );
    }

    removePreferredPromoter(promoterId: string) {
        let apiUrl = this.baseUrl + '/api/Venues/Promoters/' + promoterId;

        return this.http.delete(apiUrl).pipe(
            map((res: Response) => {
                console.log('Preferred Promoter Res: ' + res);
                return res;
            })
        );
    }

}
